import teleport from '../helpers/teleport'
// import Debug from 'debug'
// const debug = Debug('InfoQ:burger')

/* Burger toggle functionality */
const handleNavBurger = ({ classList, parentNode }) => {
  if (!classList.contains('header__toggle')) {
    return
  }

  return parentNode.classList.toggle('header--open')
}

export default {
  get header () {
    return document.querySelector('header.header')
  },

  get headerOpen () {
    return this.header && this.header.classList.contains('header--open')
      ? this.header
      : false
  },

  get searchBar () {
    return this.header.querySelector('form.search')
  },

  get defaultPosition () {
    return this.header.querySelector('.header__top .actions__right > div:first-child')
  },

  get middlePosition () {
    return this.header.querySelector('.header__middle')
  },

  init (config) {
    this.config = config
    document.addEventListener('click', e => this.clickHandler.call(e.target, this))
    window.addEventListener('resize', () => {
      const { headerOpen, searchBar, defaultPosition } = this
      if (!headerOpen || !searchBar) return
      teleport(searchBar, defaultPosition)
      headerOpen.classList.remove('header--open')
    })
  },

  clickHandler ({ headerOpen, searchBar, defaultPosition, middlePosition }) {
    if (headerOpen && !headerOpen.contains(this)) {
      headerOpen.classList.remove('header--open')
      teleport(searchBar, defaultPosition)
      return
    }
    const { nodeName, parentNode } = this
    if (nodeName !== 'BUTTON' || !(parentNode && parentNode.nodeName === 'HEADER')) {
      return
    }
    // debug('Clicked.')
    teleport(searchBar, handleNavBurger(this) ? middlePosition : defaultPosition)
  }
}
