// import { error } from '../../debug'

export default {
  get vcr () {
    const rvc = document.querySelector('.rvc.box__border')
    return rvc && rvc.children && rvc.children.length > 0 ? rvc : false
  },

  get items () {
    const { vcr } = this
    if (!vcr) return
    const ul = vcr.parentNode.querySelector('.popper ul')
    if (ul) return ul.children.length
    return 0
  },
  /**
   * Reference ITEM
   */
  get reference () {
    const { vcr: { parentNode } } = this
    return parentNode && parentNode.classList.contains('rvc__container') ? parentNode : false
  },
  get popperContentItem () {
    const { vcr } = this
    if (!vcr) return
    const { parentNode: { children } } = vcr
    return children && children[0] && children[0].classList.contains('popper') ? children[0] : false
  },
  init (config, { Popper, createPopper }) {
    const { popperContentItem, reference, vcr } = this
    if (!popperContentItem || !reference) return
    popperContentItem.style.display = 'none'

    let pop
    reference.addEventListener('mouseenter', () => {
      if (pop) return
      const { items } = this
      if (!vcr || !items) return
      pop = new Popper(vcr, createPopper(reference, popperContentItem.innerHTML, config), config)
    })
    reference.addEventListener('mouseleave', () => {
      if (!pop) return
      pop.destroy()
      pop = null
    })
  }
}
