import adblockDetect from 'adblock-detect'
// import Debug from 'debug'
// const debug = Debug('InfoQ:adblocker')

export default {

  /* Builds the HTML element for the ad blocker popup */
  createPopup (messages) {
    if (!messages || !messages.length) {
      // debug('Incorrect call of createPopup')
      return
    }
    const { infoq } = this
    if (!infoq) {
      // debug('infoq el misisng')
      return
    }
    const pop = document.createElement('DIV')
    pop.classList.add('pop', 'box', 'box__border')
    pop.innerHTML = `
      <button aria-label="Close" class="close button button__unstyled button__icon icon icon__close icon--only">Close</button>
      <div class="pop__container">
        <h3 class="heading">${messages[0]}</h3>
        <p>${messages[1]}</p>
      </div>
    `
    infoq.appendChild(pop)
    infoq.classList.add('adb')
    return pop
  },

  set adblock (flag) {
    if (!flag) {
      // debug('Not using an ad blocker.')
      return
    }

    const { strings } = this.config

    const pop = this.createPopup(strings)
    if (!pop) {
      // debug('Popup not inited')
    }
  },

  get infoq () {
    return document.getElementById('infoq')
  },

  init (config) {
    this.config = config
    const { FE } = window.JSi18n || {}
    this.config.strings = FE.adbStrings
    adblockDetect(detected => { this.adblock = detected })
  }
}
