
import {
  updateFollowButton,
  updateFollowersCount,
  hideButtonHTML,
  hiddenMetaHTML
} from './followLike'
const { labels } = window.JSi18n.FE

/**
 * Makes ['followedByCurrentUser', 'hidden'] obj keys
 * reactive in backend vars
 *
 * @param {*} vars
 * @param { event } context passed (InfoQ events)
 */
const bindReactive = (a, segment) => {
  if (!a || !(a instanceof Array) || a.length < 0) {
    // error(`invalid Array for bindReactive[${segment}]:`, a)
    return
  }
  const o = {}
  a.forEach(item => {
    let followed = item.followedByCurrentUser
    let hidden = item.hidden === 'true'
    const key = String(item.ref || item.id)
    if (!key || key === 'undefined') {
      // error('item missing id or ref', item)
      return
    }
    o[key] = item

    Object.defineProperties(o[key], {
      followedByCurrentUser: {
        set (flag) {
          const dataId = `[data-id="${this.ref || this.id}"]`
          const selectors = [
            `.${segment === 'user' ? 'author' : 'topic'}s ${dataId}`,
            `.allActivity > ul ${dataId}`
          ]
          const itemsOnPage = document.querySelectorAll(selectors)

          // For every contained item on page
          for (let container of itemsOnPage) {
            // Popper cases
            if (container.nodeName === 'A') container = container.parentNode
            if (!container) continue
            // Grab and update button
            updateFollowButton({ el: container.querySelector(`button.${flag ? 'follow' : 'unfollow'}`), segment }, flag)
            updateFollowersCount({ container }, flag)
          }
          this.followers = flag ? Number(this.followers) + 1 : Number(this.followers) - 1
          followed = flag
        },
        get () {
          return followed
        }
      },
      hidden: {
        set (flag) {
          const itemsOnPage = document.querySelectorAll(`.${segment === 'user' ? 'author' : 'topic'}s [data-id="${this.ref || this.id}"]`)

          for (let container of itemsOnPage) {
            let isAffected = container.querySelector(`a.${flag ? '' : 'un'}hide`)
            if (!isAffected) continue
            let isInsideDropdown = false
            let dropdown = null
            try {
              const { parentNode } = isAffected
              isInsideDropdown = parentNode.classList.contains('dropdown__holder')
              if (isInsideDropdown) dropdown = isAffected
            } catch (e) { throw new Error(e) }

            const meta = container.querySelector('p.meta')
            if (isAffected.classList.contains('unhide')) {
              container.classList.remove('hidden')
              const actionsRight = container.querySelector('.actions__right') || container.querySelector('.card__options .dropdown__holder')
              const hideButton = document.createElement('A')
              if (hideButton) {
                actionsRight.append(hideButton)
                hideButton.outerHTML = hideButtonHTML({
                  justIcon: !!isInsideDropdown,
                  tooltip: !!isInsideDropdown
                })
              }
              if (meta) meta.innerHTML = `${this.followers} ${labels.followers}`
            } else {
              container.classList.add('hidden')
              if (isInsideDropdown) isAffected = dropdown
              isAffected.remove()
              if (meta) meta.innerHTML = hiddenMetaHTML(this.name)
            }
          }
          hidden = flag
        },
        get () {
          return hidden
        }
      }
    })
  })
  return o
}

const segments = ['topic', 'user']
const usersTopics = Object.create(null)

segments.forEach(segment => {
  const br = bindReactive(window[`${segment}sInPage`] || [], segment)

  Object.defineProperty(usersTopics, `${segment}s`, {
    get () {
      return br
    },
    set (els) {
      if (!els) return
      Object.assign(br, bindReactive(els, segment))
    },
    enumerable: true
  })
})

const { userToken, pageSizes, uidToShow, isFollowedByCurrentUser } = (window.InfoQConstants)
const constants = { token: userToken, pageSizes, uidToShow, isFollowedByCurrentUser }

export default {
  get segments () {
    return segments
  },
  /**
   * Gets a topic's data from topicsInPage
   *
   * @param id {string} - topic's id
   * @returns data {object} - topic's data
   */
  get topic () {
    return id => usersTopics.topics[id]
  },

  /**
   * Gets a user's data from usersInPage
   *
   * @param ref {string} - user's ref (replaced from ID - why don't we just use and ID?!)
   * @returns data {object} - user's data
   */
  get user () {
    return ref => usersTopics.users[ref] || Object.values(usersTopics.users).filter(user => user.id === ref)[0]
  },

  get usersTopics () {
    return usersTopics
  },

  get constants () {
    return constants
  },

  /**
   * Gets a HTML item's "data-id" value based on the clicked followed button
   *
   * @param containerNode {HTMLElement} - the followable container
   * @returns getter {function}
   */
  get elId () {
    return containerNode => {
      const container = containerNode.closest('.popper') ||
        containerNode.closest('.subnav__content') ||
        containerNode.closest('.actions__right') ||
        containerNode.closest('li[data-id]') ||
        containerNode

      if (!container) {
        throw new Error('Invalid follow item.')
      }
      // when in popper usually
      return container.dataset.id ||
        container.parentNode.children[0].dataset.id ||
        container.parentNode.dataset.id
    }
  }
}

const likeFunc = {
  get likedContentItems () {
    return (window.userContentLikesInPage) || []
  },
  get likedCommentItems () {
    return (window.userCommentsLikesInPage) || []
  },
  get isContentLiked () {
    const { likedContentItems } = this
    return id => likedContentItems.indexOf(id) > -1
  },
  get isCommentLiked () {
    const { likedCommentItems } = this
    return id => likedCommentItems.indexOf(id) > -1
  }
}

const { users, topics } = usersTopics
export { segments, likeFunc, users, topics }
