const tabChangeEventName = 'tabChange'

/**
 * [DOM] Changes a tab
 *
 * @param {HTMLElement} instance - the .tabs instance
 * @param {Number} index - the index
 */
const handleTabChange = (instance, index, hash) => {
  const switcherEls = instance.querySelectorAll('.tab__switcher > ul > li')
  if (!switcherEls || !switcherEls.length) return

  // Remove aria selected from switch items
  for (const tabSwitchEl of switcherEls) {
    if (!tabSwitchEl) continue
    tabSwitchEl.setAttribute('aria-selected', false)
  }
  // Remove active class on all children
  for (const child of instance.children) {
    child.classList.remove('active')
  }
  // first would be for the switcher, to keep the structure minimal
  const tab = instance.children.item(index + 1)
  tab.classList.add('active')

  const { id } = tab.dataset

  if (id && hash) {
    document.location.hash = `#${id}`
  }
  const switcherItem = switcherEls.item(index)
  if (switcherItem) switcherItem.setAttribute('aria-selected', true)
}

export default {
  get tabsInstances () {
    return document.querySelectorAll('.tabs')
  },

  /**
   * Creates the HTML structure for a given .tabs > .tab element
   * @param {HTMLElement} instance
   * @param {object} options
   */
  makeTabs (instance, options, ctxEvent) {
    const tabs = instance.children
    let activeIndex = -1
    let changed = false

    if (!tabs.length) {
      // debug('No tabs found in instance: ', instance)
      return
    }

    // const event = this && this.event ? this.event : (ctxEvent || null)
    const event = window.infoq.event
    const swContainer = document.createElement('DIV')
    const switcher = document.createElement('UL')
    swContainer.classList.add('tab__switcher')
    switcher.classList.add('no-style')

    /**
     * If tabs have a data-id, switching them from hash is possible
     */
    let hash = Boolean(instance.dataset.hash) || (options && options.hash)
    activeIndex = options && options.activeIndex ? options.activeIndex : 0

    if (hash) {
      const tabsIds = Array.from(tabs).map(tab => tab.dataset.id)
      instance.dataset.hash = true
      if (window.location.hash) {
        activeIndex = tabsIds.indexOf(window.location.href.split('#')[1])
      }

      const pushUrl = (href) => {
        history.pushState({}, '', href)
        window.dispatchEvent(new Event('popstate'))
      }

      window.addEventListener('popstate', listener => {
        if (changed) return
        changed = true
        activeIndex = tabsIds.indexOf(window.location.href.split('#')[1])
        event.trigger(tabChangeEventName, {
          instance,
          payload: {
            index: activeIndex,
            hash: true
          }
        })

        if (window.location.hash) {
          pushUrl(`${window.location.href.split('#')[0]}${window.location.hash}`)
          changed = false
        }
      })
    }

    let i = 0
    for (const tab of tabs) {
      const { id, count } = tab.dataset
      if (id !== undefined && id === hash) activeIndex = i
      switcher.innerHTML += `
      <li aria-selected="${i === activeIndex}">${tab.getAttribute('data-title')}${count && Number.isInteger(Number(count)) ? ` <span class="tab__count">${count}</span>` : ''}</li>
    `
      i++
    }

    const activeItem = tabs.item(activeIndex)
    if (activeItem) activeItem.classList.add('active')

    const { heading } = instance.dataset
    if (heading) {
      const h = document.createElement('H3')
      h.textContent = heading
      h.classList.add('section__heading')
      h.classList.add('heading')
      swContainer.prepend(h)
    }
    swContainer.append(switcher)
    instance.prepend(swContainer)

    /**
     * Selects the 'active' tab
     */
    if (event) {
      event.trigger(tabChangeEventName, {
        instance,
        payload: {
          index: activeIndex,
          instanceId: instance.id,
          hash
        }
      })
    }
    instance.classList.add('tabs--inited')
  },

  init (config, context) {
    // Queries the page for tabs
    const { tabsInstances } = this
    const { event } = context
    // this.event = event
    const { length } = tabsInstances
    if (!length) {
      // debug('No tabs instances found on page, skipping.')
      return
    }
    // debug(`Initing ${length} tabs instances on page.`)

    // get each tab instance
    for (const instance of tabsInstances) {
      this.makeTabs(instance, config[instance.id || instance.dataset.id])
    }

    event.on(tabChangeEventName, e => {
      const { instance, tab, payload: { index, hash } } = e
      handleTabChange(instance, index, hash)
      event.trigger('tabChanged', { instance, tab, payload: { index, instanceId: instance.id, hash } })
      // debug(tabChangeEventName, instance, index)
    })

    document.addEventListener('click', e => {
      if (!e.target) return
      const { target } = e
      const { nodeName, parentNode: { parentNode } } = target

      // Check if clicked on a tab switcher element
      if (!parentNode ||
        nodeName !== 'LI' ||
        !parentNode.classList.contains('tab__switcher') ||
        target.getAttribute('aria-selected') === 'true') {
        return
      }

      // the tabs element
      const instance = target.closest('.tabs')

      // all children of the switcher
      const children = [].slice.call(target.parentNode.children)

      // clicked index
      const index = children.indexOf(target)

      const tab = children[index]

      // the id
      const instanceId = instance.id || instance.dataset.id

      // uses hash
      const hash = Boolean(instance.dataset.hash)

      event.trigger(tabChangeEventName, { instance, tab, payload: { index, instanceId, hash } })
    })
  }
}
