// import Debug from 'debug'
import { updateLikeButton } from '../helpers/followLike'
import { uppercase } from '../helpers'
import backendGetters, { likeFunc } from '../helpers/backendGetters'
// import { error } from '../debug'

const {
  isCommentLiked,
  isContentLiked,
  likedContentItems,
  likedCommentItems
} = likeFunc
const { user } = backendGetters
// const debug = Debug('InfoQ:like')

export default {
  init (config, context) {
    const { event, api } = context
    const { endpoint } = config

    /**
     * Register the 'like' event hook
     */
    event.on('like', async e => {
      const { flag, type, path, id, title, commenterId, commentSubject, aids, isSingle } = e
      const url = `${endpoint}!like${uppercase(type)}.action`

      if (typeof flag !== 'boolean' || !id) return

      if (type === 'content') {
        flag && !isContentLiked(id) ? likedContentItems.push(id) : likedContentItems.splice(likedContentItems.indexOf(id), 1)
      }
      if (type === 'comment') {
        flag && !isCommentLiked(id) ? likedCommentItems.push(id) : likedCommentItems.splice(likedCommentItems.indexOf(id), 1)
      }
      const selectors = [`[data-id="${id}"]`]
      if (isSingle) selectors.push('article .actions .actions__left')
      const allItemsOnPage = document.querySelectorAll(selectors)

      if (allItemsOnPage && allItemsOnPage.length) {
        for (const item of allItemsOnPage) {
          const likeBut = item.querySelector(`button.${flag ? 'like' : 'liked'}`)
          if (likeBut) updateLikeButton(likeBut, flag)
        }
      }

      let data = { f: flag }

      const { contentPath, contentTitle } = window

      Object.assign(data, (type === 'content'
        ? { id, path, title, aids }
        : { id: `${window.threadID}-${id}`, usid: commenterId, contentPath, contentTitle, commentSubject }))

      try {
        await api.post(url, data, {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
        })
      } catch (e) {
        throw new Error(e)
      }
    })

    document.addEventListener('click', e => {
      const { target } = e
      const { nodeName, classList } = target
      if (nodeName !== 'BUTTON' || !(classList.contains('like') || classList.contains('liked'))) {
        return
      }
      const { parentNode: { parentNode } } = target
      const type = parentNode.classList.contains('comment__actions') ? 'comment' : 'content'
      const isSingle = parentNode.classList.contains('article__actions')
      let el, id, flag, data, title

      switch (type) {
        case 'content':
          let path, authors
          let aids = []
          const { contentPath, contentUUID, contentTitle, authorUserCSVIds } = window

          if (isSingle) {
            path = contentPath
            id = contentUUID
            title = contentTitle
            aids = authorUserCSVIds
          } else {
            el = target.closest('.card__content')
            path = el.parentNode.dataset.path
            id = el.parentNode.dataset.id
            title = el.querySelector('.card__title a').getAttribute('title')
            authors = el.querySelectorAll(['.authors > li', '.authors > span > a'])

            if (authors && authors.length) {
              let authorId
              for (const author of authors) {
                const aId = author.dataset.id || author.dataset.ref
                const userData = user(aId)
                authorId = userData ? userData.id : null
                if (authorId && aids.indexOf(authorId) < 0) {
                  aids.push(authorId)
                }
              }
              aids = String(aids)
            }
          }

          if (!id) {
            throw new Error('Like element missing id', type)
          }

          flag = !isContentLiked(id)
          data = { id, flag, type, path, title, aids, isSingle }
          break

        case 'comment':
          el = target.closest('.comment')
          id = el.dataset.id
          flag = !isCommentLiked(id)
          const author = el.querySelector('.comment__author > a')
          const commentSubject = el.querySelector('.comment__title').textContent
          let commenterId = author.dataset.id || author.dataset.ref
          if (!commenterId) commenterId = el.querySelector('.comment__avatar').dataset.id
          data = { id, flag, contentPath, contentTitle, commenterId, commentSubject }
          break
      }

      if (!window.loggedIn) {
        event.trigger('login', { ref: 'p13n-like', segment: type, id: type === 'comment' ? `${window.threadID}-${id}` : id })
        return
      }

      Object.assign(data, { type })
      event.trigger('like', data)
    })

    this.replaceLikeStatuses()
  },

  /**
   * Gets all cards on page based on their id
   * @param {string} cardId - the id, 'data-id' of the card
   * @returns {function} - gets selected items
   */
  get matchCards () {
    return cardId => {
      return document.querySelectorAll([`.cards > li[data-id="${cardId}"]`, `article .article__actions`])
    }
  },

  /**
   * Gets all comments on page based on their id
   * @param {string} commentId - the id, 'data-id' of the comm
   * @returns {function} - gets selected items
   */
  get matchComments () {
    return commId => {
      return document.querySelectorAll(`li.comment[data-id="${commId}"]`)
    }
  },

  /**
   * Replaces 'like' buttons with 'liked' everywhere on page
   *
   * @param none
   * @returns replaced {object} - replaced items
   *
   */
  replaceLikeStatuses () {
    const { matchCards, matchComments } = this

    // Parse
    if (likedContentItems && likedContentItems.length) {
      likedContentItems.forEach(itemId => {
        const cards = matchCards(itemId)

        if (cards && cards.length) {
          for (const card of cards) {
            updateLikeButton(card.querySelector('button.like'), true)
          }
        }
      })
    }

    if (likedCommentItems && likedCommentItems.length) {
      likedCommentItems.forEach(itemId => {
        const comments = matchComments(itemId)
        if (comments && comments.length) {
          for (const comment of comments) {
            updateLikeButton(comment.querySelector('button.like'), true)
          }
        }
      })
    }
  }
}
