import Siema from 'siema'
// import Debug from 'debug'

export default {
  init (config, context) {
    const slider = document.querySelector('.slider')
    if (!slider) return
    const mySiema = new Siema(config)
    const { parentNode } = slider
    const prev = parentNode.querySelector('.prev')
    const next = parentNode.querySelector('.next')
    if (prev) prev.addEventListener('click', () => mySiema.prev())
    if (next) next.addEventListener('click', () => mySiema.next())
  }
}
