export default {
  init () {
    const video = document.getElementById('video')

    document.fullscreenEnabled =
      document.fullscreenEnabled || document.mozFullScreenEnabled || document.documentElement.webkitRequestFullScreen

    function requestFullscreen (video) {
      if (video.requestFullscreen) {
        video.requestFullscreen()
      } else if (video.mozRequestFullScreen) {
        video.mozRequestFullScreen()
      } else if (video.webkitRequestFullScreen) {
        video.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT)
      }
    }

    document.onkeydown = function (e) {
      // Play/Pause video when pressing Spacebar
      if ((e || window.event).keyCode === 32 && (document.activeElement.tagName.toLowerCase() !== 'input') && (document.activeElement.tagName.toLowerCase() !== 'textarea')) {
        video.paused ? video.play() : video.pause()
        e.preventDefault()
      }
    }

    document.onkeyup = function (e) {
      // Mute/Unmute video when pressing m
      if ((e || window.event).keyCode === 77 && (document.activeElement.tagName.toLowerCase() !== 'input') && (document.activeElement.tagName.toLowerCase() !== 'textarea')) {
        video.muted ? (video.muted = false) : (video.muted = true)
        // Forward video 10s when pressing right arrow
      } else if ((e || window.event).keyCode === 39 && (document.activeElement.tagName.toLowerCase() !== 'input') && (document.activeElement.tagName.toLowerCase() !== 'textarea')) {
        video.currentTime += 10
        // Backward video 10s when pressing left arrow
      } else if ((e || window.event).keyCode === 37 && (document.activeElement.tagName.toLowerCase() !== 'input') && (document.activeElement.tagName.toLowerCase() !== 'textarea')) {
        video.currentTime -= 10
        // Fullscreen when pressing f
      } else if ((e || window.event).keyCode === 70 && (document.activeElement.tagName.toLowerCase() !== 'input') && (document.activeElement.tagName.toLowerCase() !== 'textarea')) {
        if (document.fullscreenEnabled) {
          requestFullscreen(video)
        }
      }
    }
  }
}
