import axios from 'axios'
import { apiConfig } from './config/api'
import { EventEmitter } from 'events'
import qs from 'qs'

// eventing system
const ifqEventEmitter = new EventEmitter()
ifqEventEmitter.trigger = ifqEventEmitter.emit

// helper flags
let inited = false
let constructed = false

// modules container
const modules = {}

/**
 * Axios custom config
 */
const api = axios.create(apiConfig)
api.interceptors.request.use((request) => {
  if (request.data && (request.headers['Content-Type'].indexOf('application/x-www-form-urlencoded') !== -1)) {
    request.data = qs.stringify(request.data)
  }
  return request
})

/**
 * Main class
 *
 * @export
 * @class InfoQ
 */
export class InfoQ {
  constructor (config) {
    // Avoid constructing the class twice in the same environment
    if (constructed) {
      throw new Error('Already constructed. Please refer to one time-usage of InfoQ methods in the same environment.')
    }
    this.config = config
    constructed = true
  }

  /**
   * Private functions that handles ASYNC modules loading
   * @private
   */
  initModules () {
    if (inited) {
      throw new Error('Already inited on page, aborting.')
    }
    this.load(this.config.modules)
    inited = true
  }

  /**
   * Reserve an init function that can be called at any time
   * -- useful for hooks --
   * @returns {boolean} inited - whether it did succeed on initing or not
   */
  init () {
    this.initModules()
    return inited
  }

  /**
   * InfoQ Eventing system
   * @readonly
   */
  get event () {
    return ifqEventEmitter
  }

  /**
   * Private API methods
   * @readonly
   */
  get api () {
    return api
  }

  get modules () {
    return modules
  }

  /**
   * Modals quick API
   * infoq.modal()
   * @param {HTMLElement} element
   * @param {config}
   */
  get modal () {
    return (element, config) => {
      modules.modals.content = element
    }
  }

  /**
   * local storage, with fallback
   * -- unused currently --
   */
  get storage () {
    return window.localStorage
    // || localStorage
  }

  /**
   * unsentQueries - for offline usage
   */
  get unsentQueries () {
    const { storage } = this
    return Array(storage.getItem('unsentQueries') || [])
  }

  set unsentQueries (queries) {
    const { storage } = this
    storage.setItem('unsentQueries', queries)
  }

  /**
   * Loads all very custom modules each time inited on page.
   *
   * @readonly (to prevent loading external modules)
   * @memberof InfoQ
   * @param modules {object} - all the modules -> coming from the config file;
   * @returns void
   */
  async load (modulesToLoad) {
    const modulesKeys = Object.keys(modulesToLoad)
    if (!modulesToLoad) { throw new Error('No modules found to be inited. Aborting.') }

    await Promise.all(modulesKeys.map(async module => {
      const m = await require(`./modules/${module}.js`).default
      const moduleConfig = modulesToLoad[module]
      modules[module] = await m.init(moduleConfig, this)
    }))

    this.event.trigger('loaded')
  }

  /**
   * Dev helper
   *
   * @memberof InfoQ
   */
  refresh () {
    inited = false
  }
}
