// import Debug from 'debug'
// const debug = Debug('InfoQ:validations')
const notReqDesc = `aria-required="false"`
const notReqField = `.field:not([${notReqDesc}])`

export default {
  /**
   * Form validation handler
   *
   * @param {HTMLelement} form
   * @param {Event} submitEvent
   */
  validate (inputs, submitEvent, isLogin) {
    let hasError = false

    for (const input of inputs) {
      const { parentNode, nodeName } = input
      let { value } = input

      if (parentNode.getAttribute('aria-required') === 'false' ||
        input.getAttribute('aria-required') === 'false') continue
      let isNumeric = false
      let numVal = -1

      if (nodeName === 'SELECT') {
        numVal = Number(value)
        isNumeric = Number.isInteger(numVal)
      }

      if (!value || (isNumeric && numVal < 0)) {
        // debug('An input is missing value', input)
        hasError = true
        parentNode.classList.add('field--error')
      } else {
        parentNode.classList.remove('field--error')
      }
    }

    if (hasError && isLogin)
      submitEvent.preventDefault()

    return !hasError
  },

  init (config) {
    this.config = config

    const forms = document.querySelectorAll('form')
    if (!forms.length) {
      return
    }

    for (const form of forms) {
      /**
      * Inputs that should have a value
      */
      const inputs = form.querySelectorAll([
        `${notReqField} input[type="text"]`,
        `${notReqField} input[type="password"]`,
        `${notReqField} select`
      ])

      if (!inputs || !inputs.length)
        continue

      const submit = form.querySelector('input[type="submit"]')
      if (!submit) continue

      submit.addEventListener('click', e => {
        if (submit && submit.disabled) {
          return
        }

        //scroll first error into view
        setTimeout(() => {
          const firstErrorField = form.querySelector('.field--error')
          if (!firstErrorField)
            return

          firstErrorField.scrollIntoView()
        }, 250)

        return this.validate(inputs, e, form.classList.contains('login__form'))
      })
    }
  }
}
