// import Debug from 'debug'
// const debug = Debug('InfoQ:comments')

export default {
  get el () {
    return document.querySelector('.comments > .comments__list')
  },
  init (config, { Popper, createPopper }) {
    const { el } = this
    if (!el) return
    const comms = el.querySelectorAll('li[data-id]')
    if (!comms) return
    for (const comm of comms) {
      const {id} = comm.dataset
      if (!id) {
        // debug('Comment missing ID', comm)
        continue
      }

      /**
       * Clone comment element &
       * Create poppers on hover
       */
      comm.addEventListener('mouseenter', e => {
        const comment = `.comment[data-id="${id}"]`
        const reference = document.querySelector(comment)
        if (!reference) {
          // debug(`missing reference item ${comment}`)
          return
        }

        // creates a clone of the reference
        const clone = reference.cloneNode(true)
        const nested = clone.querySelectorAll(['.comment', '.reply__box'])
        clone.classList.add('comment')
        clone.id = ''

        // delete backToTop items
        const backToTop = clone.querySelector('a.top')
        if (backToTop) backToTop.remove()

        // remove edit item
        const editButton = clone.querySelector('a.edit')
        if (editButton) editButton.remove()

        // const replyButton = clone.querySelector('button.reply')

        // deletes nested comments from clone
        for (const item of nested) item.remove()

        // adjust title
        const commTitle = comm.querySelector('.comment__title a')
        const refTitle = clone.querySelector('.comment__title')
        const newTitle = document.createElement('SPAN')
        newTitle.textContent = refTitle.textContent
        newTitle.classList.add('comment__title')
        refTitle.parentNode.prepend(newTitle)
        refTitle.remove()
        const { outerHTML } = clone

        // create the popper
        /* eslint-disable no-new */
        new Popper(commTitle, createPopper(commTitle, outerHTML), config)
        /* eslint-enable no-new */
      })

      comm.addEventListener('mouseleave', e => {
        const { target } = e
        const popper = target.querySelector('.popper')
        if (!popper) return
        popper.remove()
      })
    }

    document.addEventListener('click', e => {
      if (!e) return
      const { target } = e
      if (target.nodeName !== 'BUTTON' || !target.classList.contains('reply')) return
      const com = target.closest('li.comment')
      const { parentNode } = com
      if (!parentNode) return
      const { classList } = parentNode
      if (!classList.contains('popper')) return
      const { id } = com.dataset
      if (!id) {
        // debug('Comment missing id', com)
        return
      }
      document.location.hash = `anch${id}`
      const refComment = document.querySelector(`.comments__boxes li.comment[data-id="${id}"]`)
      // if (!refComment) debug('Missing ref comment', id)
      const replyBut = refComment.querySelector('button.reply')
      if (replyBut) setTimeout(() => { replyBut.click() }, 100)
    })
  }
}
