import followButtonsLabels from '../config/defaults/labels/followButtons'
import likeButtonsLabels from '../config/defaults/labels/likeButtons'
// import { error } from '../debug'
import backendGetters from '../helpers/backendGetters'
import { populate } from '../modules/picks'
const { constants: { uidToShow } } = backendGetters

/**
 * Buttons' labels and defaults
 */
const { labels } = window.JSi18n.FE
const followLabels = labels || followButtonsLabels
const likeLabels = labels || likeButtonsLabels

/**
 * Follow Button - Label
 *
 * @param {boolean} flag - button status
 * @param {boolean} isTopic - if it's a topic button
 * @returns {string} - the label, eg: 'Follow Topic'
 */
const followButtonLabel = (flag, isTopic) => {
  const { followTopic, unfollowTopic, follow, unfollow } = followLabels
  return flag
    ? (isTopic ? unfollowTopic : unfollow)
    : (isTopic ? followTopic : follow)
}

/**
 * Like Button - Label
 *
 * @param {boolean} flag - button status
 * @returns {string} - the label, eg: 'Liked'
 */
const likeButtonLabel = flag => {
  const { like, liked } = likeLabels
  return flag ? liked : like
}
const hiddenLabel = labels.hidden || '%s is hidden'

export const hideButtonHTML = ({ justIcon, tooltip }) => `<a class="close hide button__unstyled ${justIcon ? 'icon--only' : ''} ${tooltip ? 'button__tooltip' : ''}" ${tooltip ? `data-tip="${labels.hide}"` : ''} aria-label="${labels.hide}">${labels.hide}</a>`
export const hiddenMetaHTML = name => `${hiddenLabel.replace('%s', name)} <a class="unhide button__unstyled">${labels.undo || 'Undo'}</a>`
const cardOptions = window.currentUserId === uidToShow ? `<div class="card__options dropdown"><button class="button button__arrow button__small button__unstyled"></button><div class="dropdown__holder">${hideButtonHTML({ justIcon: false, tooltip: false })}</div></div>` : ''

/**
 * Creates a follow button HTMLElement
 *
 * @param flag {boolean} - button status
 * @param size {string} - button size
 * @param segment {string} - user | topic
 * @returns {HTMLElement} - newly created button
 */
const createFollowButton = ({ flag, size, segment }) => {
  const button = document.createElement('BUTTON')
  button.classList.add('button')
  button.classList.add(`button__${size !== undefined ? size : 'small'}`)
  button.classList.add(`${segment === 'user' ? 'author' : segment}__follow`)
  button.classList.add(flag ? 'unfollow' : 'follow')
  const text = followButtonLabel(flag, segment === 'topic')
  button.textContent = text
  button.setAttribute('aria-label', text)
  return button
}

/**
 * Creates the -entire- HTML element for the followable item
 * @param {object} config - config items
 */
const createFollowable = ({ segment, item, data, hideButton, asCards, isSegmentOfPS }) => {
  if (!item || !data) {
    throw new Error('Missing item data to create pick.')
  }

  if (isSegmentOfPS) {
    const { name, title, type, followers, followedByCurrentUser, url, id, imgSrc, hidden, link, meta, date } = data
    const isTopic = segment === 'topic'
    const cls = isTopic ? 'topic__follow' : 'author__follow'
    const avatar = !isTopic || imgSrc
    const href = link || url
    if (!href) {
      throw new Error(`missing url for item ${{ title, name, type }}`)
    }
    const shouldShowFollowButton = String(id) !== String(window.currentUserId)
    const followButtonHTML = shouldShowFollowButton ? `<button class="button button__small ${followedByCurrentUser ? 'unfollow' : 'follow'} ${cls}">${followButtonLabel(followedByCurrentUser, isTopic)}</button>` : ''
    const itemHTML = `<h5 class="heading ${asCards ? 'card__title' : ''}"><a href="${href}">${name || title}</a></h5>
    <p class="meta">${hidden && hideButton ? hiddenMetaHTML(name) : followers !== undefined ? `${followers} ${labels.followers}` : ''}</p>
    ${asCards ? followButtonHTML : ''}`

    if (!asCards) item.classList.add('actions')
    if (hidden && hideButton) item.classList.add('hidden')
    if (type) item.dataset.type = type

    item.innerHTML = `
      ${asCards ? `<div class="card__content">${hideButton && asCards ? cardOptions : ''}<div class="card__data">` : ''}
      ${asCards && isSegmentOfPS && !isTopic ? `<div class="author__card">` : ``}
      ${!asCards ? `<div class="actions__left${isTopic ? '' : ' author__card'}">` : ''}
      ${(avatar
    ? `<a class="author__avatar avatar" href="${href}">${imgSrc ? `<img src="${imgSrc}" />` : ''}</a>
          <div class="author__info">${itemHTML}</div>`
    : itemHTML
  )}
      ${!asCards ? `</div>` : ''}
      ${asCards && isSegmentOfPS && !isTopic ? `</div>` : ``}
      ${!asCards ? `<div class="actions__right">
          ${followButtonHTML}
          ${(hideButton && !hidden ? hideButtonHTML({ tooltip: true, justIcon: true }) : '')}
        </div>` : ''}
      ${asCards && meta && date ? `<div class="card__footer">
        <div class="card__details">
          <span>${meta}&nbsp;</span>
          <span class="date">${date}</span>
        </div>
      </div>` : ''}
      ${asCards ? '</div></div>' : ''}`
  } else if (asCards) {
    const { title, date, meta, body, link, path, imgSrc, type } = data
    if (path) item.dataset.path = path
    const isArticle = ['articles', 'reviewed'].indexOf(segment) > -1
    const taxesWithIcons = ['presentations', 'podcasts', 'interviews']
    const hasIcon = taxesWithIcons.indexOf(segment) > -1 || taxesWithIcons.indexOf(type) > -1

    // add the tax class to icons with icon
    if (hasIcon) {
      item.classList.add(type || segment)
    }

    const cardHeader = imgSrc ? `
      <a href="${link}" class="card__header">
        <img src="${imgSrc}" alt="${title}" class="card__image" />
        ${hasIcon ? `<div class="card__meta"><div class="card__icon"></div></div>` : ''}
      </a>` : ''
    item.innerHTML = `
      <div class="card__content">
        ${isArticle ? cardHeader : ''}
        <div class="card__data">
          <h3 class="card__title"><a href="${link}">${title}</a></h3>
          ${body !== undefined ? `<p class="card__excerpt">${body}</p>` : ''}
          <div class="card__footer">
            <div class="card__details">
              <span>${meta}&nbsp;</span>
              <span class="date">${date}</span>
            </div>
          </div>
        </div>
        ${!isArticle ? cardHeader : ``}
      </div>
    `
  }
}

/**
 * [DOM] Updates a follow button's attributes & value
 *
 * @readonly
 * @param flag {boolean} - following / unfollowing
 */
const updateFollowButton = (context, flag) => {
  const { el, segment } = context
  if (!el || !segment) return

  const { classList } = el

  // yes, two classes, lame, will change this.
  classList.toggle('follow', !flag)
  classList.toggle('unfollow', flag)

  // Setup new button values
  const text = followButtonLabel(flag, segment === 'topic')
  el.textContent = text
  el.setAttribute('aria-label', text)
}

/**
 * [DOM] Updates a LIKE button's attributes & value
 *
 * @readonly
 * @param ctx {object} - context passed to function
 * @param flag {boolean} - following / unfollowing
 */
const updateLikeButton = (el, flag) => {
  if (!el) return
  const { classList } = el

  // yes, two classes, lame, will change this.
  const butClass = classList.contains('button__green') || classList.contains('button__gray')
  if (flag) {
    classList.add('liked')
    classList.add(`${butClass ? 'button' : 'icon'}__green`)
    classList.remove('like')
    classList.remove(`${butClass ? 'button' : 'icon'}__gray`)
  } else {
    classList.add('like')
    classList.add(`${butClass ? 'button' : 'icon'}__gray`)
    classList.remove('liked')
    classList.remove(`${butClass ? 'button' : 'icon'}__green`)
  }

  // Setup new button values
  const text = likeButtonLabel(flag)
  el.textContent = text
  el.setAttribute('aria-label', text)
}

/**
 * [DOM] Updates a container's followers' count if it exists.
 * @called only in context, @readonly
 * --! Shouldn't affect the string at all (to keep in mind for i18n)
 *
 * @param newValue {number} -> Value to update to
 */
const updateFollowersCount = (context, flag) => {
  const { container } = context
  const { followers } = labels
  const ps = container.querySelectorAll('p.meta')

  for (const p of ps) {
    let { textContent } = p
    if (textContent.indexOf(followers) > -1) {
      let words = textContent.split(' ')
      words.forEach((word, i) => {
        if (word === followers) return
        // counter adjustments
        if (word && Number(word) > -1) words[i] = flag ? Number(words[i]) + 1 : Number(words[i]) - 1
      })
      p.textContent = words.join(' ')
    }
  }
}

/**
 * Builds the popper markup for user & topics
 *
 * @param segment {string} - user | topic
 * @param data {object} authorProfile / topicData
 * @returns {string:HTML} - the HTML for the popper
 */
const popperHTML = (segment, data) => {
  const { name, minibio, followedByCurrentUser, followers, imgSrc, url, link, showShortBio } = data
  const { outerHTML } = createFollowButton({
    flag: followedByCurrentUser,
    segment
  })

  return segment === 'user' ? `
      <div class="author__about author">
        <div class="author__card">
          <a href="${url}" class="avatar author__avatar">${imgSrc ? `<img src="${imgSrc}" />` : ''}</a>
          <div class="author__info">
            <h3 class="author__name"><a href="${url}">${name}</a></h3>
            ${showShortBio ? `<p class="author__bio meta">${minibio}</p>` : ''}
            ${outerHTML}
          </div>
          ${followers !== undefined ? `<p class="meta author__followstats">${followers} ${followLabels.followers}</p>` : ''}
        </div>
      </div>`
    : `<h3 class="heading"><a href="${link || url}">${name}</a></h3>
       ${followers !== undefined ? `<p class="meta">${followLabels.followers || 'Followers'}: ${followers}</p>` : ''}
       ${outerHTML}`
}

/**
 * Registers the event triggering for "hide item" where required
 * @returns void
 */
const hideItemHandler = (event) => {
  document.addEventListener('click', e => {
    const { target } = e
    const { nodeName, classList } = target
    if (nodeName !== 'A' || !(classList.contains('hide') || classList.contains('unhide'))) return
    let type

    const targetParent = target.closest('[data-id]')
    const id = targetParent.dataset.id
    if (!id) throw new Error('undefined ID for item clicked to be hidden', targetParent)
    const ul = targetParent.parentNode.parentNode
    if (!ul) return
    type = ul.classList.contains('topics') ? 'ft' : (ul.classList.contains('authors') ? 'fu' : null)
    if (!type) throw new Error('could not determine if topic or author', ul)
    const isHidden = backendGetters.usersTopics[type === 'ft' ? 'topics' : 'users'][id].hidden

    event.trigger('hideProfileItem', {
      type,
      id,
      isHidden
    })
  })
}

function populateSearchTopicsSection (results) {
  const searchPage = document.querySelector('.search_page.results')
  if (!searchPage) return

  if (!(results && results.length)) {
    throw new Error('incorrect results or no results')
  }

  const topics = searchPage.querySelector('.topics ul')

  // clear list after page check
  topics.innerHTML = ''
  window.usersTopics.topics = results

  results.forEach(topic => {
    const li = document.createElement('LI')
    li.dataset.id = topic.id
    topics.append(li)
  })

  topics.parentNode.classList.remove('preload')

  populate({
    segment: 'topic',
    selector: '.search_page .topics',
    createFromScratch: true,
    isSegmentOfPS: true,
    asCards: false,
    hideButton: false
  })
}

export {
  createFollowButton,
  createFollowable,
  hideItemHandler,
  updateLikeButton,
  updateFollowButton,
  updateFollowersCount,
  popperHTML,
  populateSearchTopicsSection
}
