
import backendGetters from '../helpers/backendGetters'
import { uppercase } from '../helpers'
// import { error } from '../debug'
import { populate } from './picks'
import { createFollowable, hideItemHandler } from '../helpers/followLike'
import { getAllActivityIndexes } from './profile'

const {
  elId,
  usersTopics,
  constants,
  user
} = backendGetters

const { token, pageSizes, uidToShow } = constants

const hideItemAction = '/fandl!hideItem.action'
const picksAction = '/usersTopicsPicksDetails.action'
const profileAction = '/userProfile!getMore.action'

/**
 * Follow users and topics functionality
 * @module
 */
export default {
  /**
   * Init function - Module constructor
   *
   * @param {object} module config
   * @param {object} passed context to module
   */
  init (config, context) {
    const { api, event } = context
    const { showMoreItems, zones, endpoint } = config

    /**
     * Final indicator for all PS population zones
     */
    let populated = false
    const { labels } = window.JSi18n.FE

    /**
     * A flag that determines if data is supplied
     * in a var in the current page HEAD
     * or instead do an API request
     */
    let hasUserTopics =
      (window.usersInPage && window.usersInPage.length > 0) ||
      (window.topicsInPage && window.topicsInPage.length > 0) ||
      (window.followedUsersInPage)

    /**
     * API request to get the data
     *
     * !response comes in string format, not JSON
     */
    event.on('getUserTopics', async e => {
      try {
        let { data } = await api.get(picksAction)

        // make the data look more like a json...
        data = data.replace('users:', '{"users":')
        data = data.replace(']', '],\n')
        data = data.replace('topics:', '"topics":')
        data = data.replace(/[/\\*]/g, '')
        data += '}'
        data = JSON.parse(data)
        const { users, topics } = data

        if (users && users instanceof Array && users.length > 0) { usersTopics.users = users }
        if (topics && topics instanceof Array && topics.length > 0) { usersTopics.topics = topics }
        event.trigger('readyToPopulate')
      } catch (err) {
        throw new Error('getUserTopics request failed: ', err)
      }
    })

    // Do the API request if the inidicator is negative
    if (!hasUserTopics) {
      event.trigger('getUserTopics')
    }

    /**
     * Editor's picks, topics & peers you follow
     */
    const picks = document.querySelectorAll('.picks')
    const peersYouFollow = document.querySelector('.peers-you-follow')
    const topicsYouFollow = document.querySelector('.topics-you-follow')

    event.on('hideProfileItem', async e => {
      const { id, type, isHidden } = e
      try {
        await api.get(hideItemAction, {
          params: { id, type }
        })
        usersTopics[type === 'ft' ? 'topics' : 'users'][id].hidden = !isHidden
      } catch (e) { throw new Error(e) }
    })

    // add event listeners for hide button
    if (peersYouFollow || topicsYouFollow) {
      hideItemHandler(event)
    }

    event.on('getStuffYouFollow', async e => {
      const { element, asCards } = e
      let { selector, segment } = e

      if (!element) return

      // counter - how many items to show, fallback if not defined on backend
      // uses same number for both topics and users :)
      if (!token) {
        throw new Error('userToken in InfoQConstants is undefined')
      }

      // paging index
      let index = e.index && e.index > -1 ? e.index : 0
      const otherKeys = e.otherKeys || {}
      const single = e.single !== undefined ? e.single : true
      let isSegmentOfPS = ['user', 'topic', 'followers'].indexOf(segment) > -1
      let key = segment === 'topic' ? 'ft' : segment === 'user' ? 'fu' : segment === 'followers' ? 'f' : segment
      if (segment === 'comments') key = 'cmo'
      if (segment === 'likes') {
        key = 'lc'
        Object.assign(otherKeys, {
          cml: 0
        })
      }
      if (!selector) {
        if (segment === 'followers') {
          selector = '.followers'
        } else {
          selector = `.${isSegmentOfPS ? `${segment === 'user' ? 'peers' : 'topics'}-you-follow` : segment}`
        }
      }
      event.trigger('setItemsIndex', { segment, newIndex: index })
      segment = segment === 'followers' ? 'user' : segment
      let hasPreloader = element.querySelector('div.preload')

      try {
        const showMoreMultiplier = pageSizes[key] || showMoreItems

        const postData = {
          params: {
            [key]: index * showMoreMultiplier,
            single,
            token,
            ...otherKeys
          }
        }

        if (segment === 'allActivity') {
          delete postData.params[segment]
        }

        let { data } = await api.get(profileAction, postData)

        if (!data && !data.length) {
          throw new Error('Invalid data')
        }

        let ul = element.querySelector('ul')
        element.classList.add(isSegmentOfPS ? segment === 'user' ? 'authors' : 'topics' : segment)
        let hasMore = data && data.length

        if (isSegmentOfPS) {
          data.forEach(user => {
            user.name = user.title
            delete user.title

            user.followers = Number(user.count)
            delete user.count

            if (!user.hasMore) hasMore = false
          })
          usersTopics[`${segment}s`] = data
        }

        const lis = new Map()
        data.map(item => {
          lis.set(item.id, item)
          if (!item.hasMore) hasMore = false
        })

        // List & elements
        if (!ul) {
          ul = document.createElement('UL')
          ul.classList.add('no-style')
        }

        if (asCards) {
          ul.classList.add('cards')
          ul.classList.add('boxes') // ie10, polyfill fail

          if (!isSegmentOfPS) {
            let cols = ['interviews', 'presentations', 'minibooks'].indexOf(segment) > -1 ? 3 : 1
            const horizontalAndSingle = ['likes', 'comments', 'allActivity', 'podcasts', 'news', 'articles', 'research', 'reviewed'].indexOf(segment) > -1
            if (horizontalAndSingle) cols = 1

            Object.assign(ul.dataset, {
              tax: segment,
              size: 'medium',
              cols
            })
            // kinda tricky
            if (horizontalAndSingle && segment.indexOf('podcasts') > -1) {
              ul.dataset.tax = 'podcasts'
              ul.dataset.size = 'medium'
            }
            if (['articles', 'reviewed', 'research', 'podcasts', 'comments', 'likes', 'allActivity', 'news', 'articles'].indexOf(segment) > -1) {
              Object.assign(ul.dataset, {
                horizontal: true
              })
            }
          }
        } else {
          ul.classList.add('ul__border')
        }

        // Show more button
        let but = element.querySelector('button.more')
        if (hasMore && !but) {
          but = document.createElement('BUTTON')
          but.classList.add('button')
          but.classList.add('button__large')
          but.classList.add('more')

          but.textContent = labels.showMore || 'Show more'
          but.addEventListener('click', e => {
            but.remove()
            but = null
            let preloadDiv
            if (hasMore && (['fu', 'ft'].indexOf(key) < 0) && !hasPreloader) {
              preloadDiv = document.createElement('DIV')
              preloadDiv.classList.add('preload')
            }
            if (preloadDiv && hasMore) element.append(preloadDiv)
            if (segment !== 'allActivity') {
              index += 1
              event.trigger('getStuffYouFollow', {
                element,
                index,
                segment: key === 'f' ? 'followers' : segment,
                asCards
              })
            } else {
              const otherKeys = getAllActivityIndexes(data)
              event.trigger('getStuffYouFollow', {
                segment,
                element,
                single: false,
                asCards,
                otherKeys
              })
            }
          })
        }

        /**
         * Remove the 'MORE' button
         */
        if (!hasMore && but) {
          but.remove()
          but = null
        }

        /**
         * Creates the list items,
         * fills them with data and
         * populates them with 'followables'
         */
        const lisIterator = lis.keys()

        // empty message
        if (!lis.size) {
          if (topicsYouFollow || peersYouFollow) {
            const li = document.createElement('LI')
            ul.append(li)
            li.textContent = labels[`no${segment.charAt(0).toUpperCase() + segment.slice(1)}`] || 'No data.'
          }
        }

        for (const item of lisIterator) {
          const li = document.createElement('LI')
          li.dataset.id = item
          ul.append(li)

          if (!isSegmentOfPS) {
            let itemData = lis.get(item)
            const { type, id } = itemData
            const isPS = ['ft', 'fu'].indexOf(type) > -1
            const PStype = type === 'ft' ? 'topic' : 'user'
            if (isPS && PStype) {
              usersTopics[`${PStype}s`] = [itemData]
              itemData = backendGetters[PStype](id)
            }
            createFollowable({
              segment: isPS ? PStype : segment,
              data: itemData,
              isSegmentOfPS: isPS,
              item: li,
              hideButton: false,
              asCards: true
            })
          }
        }

        // Drop the preloading
        element.classList.remove('preload')

        // Append the list
        element.append(ul)

        populate({
          segment,
          selector,
          createFromScratch: true,
          hideButton: String(uidToShow) === String(window.currentUserId),
          asCards,
          isSegmentOfPS
        })
        if (but) element.append(but)
        ul.classList.remove('preload')
        if (hasPreloader) {
          hasPreloader.remove()
        }
      } catch (err) {
        throw new Error(err)
      }
    })

    if (peersYouFollow) {
      event.trigger('getStuffYouFollow', {
        element: peersYouFollow,
        segment: 'user'
      })
    }

    if (topicsYouFollow) {
      event.trigger('getStuffYouFollow', {
        element: topicsYouFollow,
        segment: 'topic'
      })
    }

    /**
     * Register the follow event hook
     * @private
     */
    event.on('follow', async e => {
      let { segment, id, flag } = e

      try {
        await api.post(`${endpoint}!follow${uppercase(segment)}.action`, { id, f: flag })
      } catch (e) {
        // unsentQueries += {url: data}
        throw new Error(e)
      }
    })

    // can be twice per page, in nav drop & EP page
    let test = 0
    for (const pick of picks) {
      if (test) pick.classList.add('helper')
      event.trigger('getEditorPicks', { element: pick })
      test += 1
    }

    /**
     * Populates all HTMLelements based on their 'zones' with follow buttons
     */
    event.on('readyToPopulate', () => {
      if (populated) return

      Object.keys(zones).forEach(zone => {
        zones[zone].forEach(populable => {
          populate(Object.assign(populable, { segment: zone }))
        })
      })

      populated = true
    })

    if (hasUserTopics) {
      // notifs grp page
      const { followedUsersInPage } = window
      if (followedUsersInPage && followedUsersInPage) {
        const allUsersElements = document.querySelectorAll('.authors li[data-id]')

        const users = []
        allUsersElements.forEach(userEl => {
          const { id } = userEl.dataset
          if (!id) return
          users.push(id)
        })

        usersTopics.users = users.map(id => ({
          id,
          followedByCurrentUser: false
        }))
        followedUsersInPage.forEach(userId => {
          const _user = user(userId)
          if (!_user) return
          _user.followedByCurrentUser = true
        })
      }
      event.trigger('readyToPopulate')
    }

    // Handle follow buttons on click
    document.addEventListener('click', e => {
      const { target } = e
      const { nodeName, classList, parentNode } = target

      if (nodeName !== 'BUTTON' ||
        !(classList.contains('topic__follow') ||
          classList.contains('follow') ||
          classList.contains('unfollow') ||
          classList.contains('author__follow'))) {
        return
      }

      let segment = classList.contains('topic__follow')
        ? 'topic' : 'user'

      const ref = elId(parentNode)

      if (!ref) {
        throw new Error('data-id not found on element: ', parentNode)
      }
      const item = backendGetters[segment](ref)

      if (!item) {
        throw new Error('could not find', segment, ref, ' in vars')
      }
      const { id } = item

      if (!id) {
        throw new Error('invalid item ID supplied in vars', item)
      }

      let trkRef = null
      const trkRefEl = parentNode.closest('[data-trk-ref]')
      trkRef = trkRefEl ? trkRefEl.dataset.trkRef : null

      if (!window.loggedIn) {
        event.trigger('login', { ref: 'p13n-follow', segment, id })
        return
      }

      const flag = item.followedByCurrentUser
      if (flag === 'undefined') {
        throw new Error('Could not get follow status', segment, id)
      }
      item.followedByCurrentUser = !flag
      event.trigger('follow', { trkRef, segment, id, flag: !flag })
    })
  }
}
