import { popperHTML } from '../../helpers/followLike'
import backendGetters from '../../helpers/backendGetters'

const { topic, user } = backendGetters

export default {
  get cardAuthors () {
    const selector = 'card__authors'
    return card => {
      const holder = card.querySelector(`.${selector}`)
      const items = card.querySelectorAll(`.${selector} > span`)
      return {
        holder, items
      }
    }
  },

  get cardTopics () {
    const selector = 'card__topics'
    return card => {
      const holder = card.querySelector(`.${selector}`)
      const items = card.querySelectorAll(`.${selector} > span`)
      return {
        holder, items
      }
    }
  },

  /**
   * Deletes all poppers previously created for a card
   * @param {HTMLElement} card - card item
   */
  deletePoppers (card) {
    const activePoppers = card.querySelectorAll('.popper')
    if (!activePoppers) {
      return
    }
    for (const popper of activePoppers) {
      popper.remove()
    }
  },

  /**
   * Creates poppers for all cards on page!
   *
   * @param {object: {authors, topics}} itemsData - passed items data through context
   * @param {object} InfoQPopperMethods
   */
  createPoppers (itemsData, { Popper, createPopper }) {
    const { config } = this
    const { showShortBio } = this

    Object.keys(itemsData).forEach(section => {
      const segment = section === 'authors' ? 'user' : 'topic'
      const items = itemsData[section]
      if (!items) return

      for (const item of items) {
        const link = item.children[0]
        if (!link || !link.href) {
          throw new Error('Incorrect item definition on item: ', item)
        } else {
          const id = link.getAttribute('data-id')
          if (!id) continue

          const data = this[segment](id)
          if (!data) continue

          if (!data.link && link.href) {
            Object.assign(data, { link: link.href })
            if (section === 'user') {
              Object.assign(data, { showShortBio })
            }
          }
          /* eslint-disable no-new */
          new Popper(link, createPopper(item, popperHTML(segment, data)), config)
        }
      }
    })
  },

  /**
   * Module constructor
   * @param {*} config
   * @param {*} context
   */
  init (config, context) {
    this.config = config

    // Bind topic & user getters
    this.topic = topic
    this.user = user

    const { cardAuthors, cardTopics } = this
    const { cards } = context

    for (const card of cards) {
      const topics = cardTopics(card)
      const authors = cardAuthors(card)
      // Bind events only if they either have authors or topics
      if (topics.items.length) {
        topics.holder.addEventListener('mouseenter', () => this.createPoppers({ topics: topics.items }, context))
        topics.holder.addEventListener('mouseleave', () => this.deletePoppers(card))
      }

      if (authors.items.length) {
        authors.holder.addEventListener('mouseenter', () => this.createPoppers({ authors: authors.items }, context))
        authors.holder.addEventListener('mouseleave', () => this.deletePoppers(card))
      }
    }
  }
}
