import { focusOnFirstInput } from '../helpers'
// import Debug from 'debug'
// const debug = new Debug('InfoQ:Dropdowns')

let notifTriggered = false

export default {
  init (config, ctx) {
    this.config = config
    this.event = ctx.event
    this.event.on('notificationsTriggered', e => {
      if (e.badge) e.badge.remove()
    })
    document.addEventListener('click', e => this.clickHandler.call(e.target, this))
  },

  get active () {
    return document.querySelector(`.${this.openClass}`)
  },

  get openClass () {
    return this.config.openClass || 'dropdown--open'
  },

  handle (parent, openClass, event) {
    if (!parent) return
    const { classList } = parent

    if (!classList || !classList.contains('dropdown')) {
      return
    }

    classList.toggle(openClass)

    // Login button? Focus on email
    if (classList.contains('user__login')) {
      focusOnFirstInput(parent)
    }

    // If clicked on user notifcations, remove the badge
    if (classList.contains('user__notifications')) {
      const badge = parent.querySelector('.badge')
      if (!notifTriggered) event.trigger('notificationsTriggered', { badge })
    }
  },

  /*
   * Close open dropdowns if not clicked inside the actual one;
   * reproduces the clickaway effect
   */
  closeOpen (active, context, openClass) {
    if (!active) {
      return
    }
    if (!active.contains(context)) {
      active.classList.remove(openClass)
    }
  },

  clickHandler ({ closeOpen, handle, active, openClass, event }) {
    if (!this) return
    const { nodeName, parentNode } = this
    closeOpen(active, this, openClass)
    if (nodeName !== 'BUTTON') {
      return
    }
    handle(parentNode, openClass, event)
  }
}
