const createModal = () => {
  const modal = document.createElement('DIV')
  modal.classList.add('modal')
  modal.innerHTML = `
      <div class="modal__backdrop">
        <button class="close modal__close">Close</button>
      </div>
      <div class="modal__content"></div>`
  return modal
}
let movedParentNode = null
let triggered = false

/**
 * infoq.modal()
 */
export default {
  get body () {
    return document.getElementById('infoq')
  },
  get modal () {
    return document.querySelector('.modal')
  },
  // we no longer use this, commented for Edge
  // enable debugger -> to check, if needed

  // handleExistingModalsInPage (api) {
  //   if (triggered) return
  //   const popups = document.querySelector('.popups')
  //   if (!popups) return
  //   const modalsToShow = popups.querySelectorAll(':scope > div')
  //   if (!modalsToShow || modalsToShow.length < 0) return

  //   for (const modal of modalsToShow) {
  //     if (triggered) break
  //     const { targetUrihash } = modal.dataset
  //     if (!targetUrihash) continue
  //     if (`#${targetUrihash}` !== document.location.hash) continue

  //     api.content = modal
  //   }
  // },

  init (config, { event }) {
    const { body, modal } = this
    if (modal) { modal.remove() }

    let publicApi = Object.create(null)

    body.appendChild(createModal())

    event.on('modalOpen', e => {
      movedParentNode = e.parentNode
      body.classList.add('modal--open')
      triggered = true
    })

    const modalContent = this.modal.querySelector('.modal__content')
    const modalBackdrop = this.modal.querySelector('.modal__backdrop')

    const triggerModalClose = e => {
      event.trigger('modalClose', modalContent.children[0])
    }

    modalBackdrop.addEventListener('click', triggerModalClose)

    // safari
    document.addEventListener('touchstart', e => {
      if (!triggered) return
      e.preventDefault()
    })

    event.on('modalClose', e => {
      try {
        body.classList.remove('modal--open')
        const child = modalContent.children[0]
        if (movedParentNode && child) {
          movedParentNode.append(child)
        }
        modalContent.innerHTML = ''
        triggered = false
      } catch (err) {}
    })

    Object.defineProperty(publicApi, 'content', {
      configurable: true,
      get () {
        return modalContent.innerHTML
      },
      set (el) {
        modalContent.innerHTML = ''
        const { parentNode, dataset: { expires } } = el
        modalContent.appendChild(el)

        if (expires && typeof expires !== 'undefined' && Number(expires)) {
          setTimeout(() => { event.trigger('modalClose') }, expires)
        }

        event.trigger('modalOpen', { parentNode })
      }
    })

    // this.handleExistingModalsInPage(publicApi)

    return publicApi
  }
}
