import { focusOnFirstInput, uppercase } from '../helpers'
import backendGetters from '../helpers/backendGetters'
// import Debug from 'debug'
// const debug = Debug('InfoQ:login')
const { user } = backendGetters

// window.user = user
const modalContent = '.modal__content'
export default {
  get loginForm () {
    return document.querySelector(`${modalContent} .login__form`) || document.querySelector('.login__form')
  },
  get formsContainer () {
    return document.querySelector(`${modalContent} .login__dropdown`) || document.querySelector('.login__dropdown')
  },
  get activeForm () {
    return form => form.classList.contains('hide')
      ? form.parentNode.querySelector(`.forgot__form`)
      : form
  },
  get initialPosition () {
    return document.querySelector('.dropdown.user__login .dropdown__content')
  },
  /**
   * *Puts* back the form to it's original position
   * @param {HTMLElement} item
   * @param {HTMLElement} intialPosition
  */
  restore (item, initialPosition) {
    const { classList } = item

    classList.remove('hide')
    const hiddenInputs = item.querySelectorAll('input[type="hidden"]')

    for (const input of hiddenInputs) {
      if (input.id === 'loginWidgetOrigin') input.value = 'mainLogin'
      else input.value = null
    }
    for (const child of initialPosition.children) {
      child.remove()
    }
    initialPosition.prepend(item)
  },
  updateHiddenInput (form, { name, id, value }) {
    const input = form.querySelector(`input[type="hidden"][name="${name}"]`)
    if (!input) return
    input.value = value
  },
  init (cfg, ctx) {
    const { event, modal } = ctx
    const { restore, initialPosition, updateHiddenInput } = this

    event.on('login', e => {
      // item data-ref
      const { ref, segment, id } = e

      modal(this.formsContainer)
      focusOnFirstInput(this.loginForm)

      if (!ref) return
      const fromP13N = ref.indexOf('p13n-') > -1
      if (fromP13N && segment && id) {
        const inputs = [{ name: 'fromP13N', value: true },
          { name: 'fromP13NId', id: 'p13n-id', value: segment === 'user' ? user(id).id : id },
          { name: 'fromP13NType', id: 'p13n-type', value: ref.split('-')[1] === 'follow' ? `follow${uppercase(segment)}` : segment }]

        inputs.forEach(input => {
          updateHiddenInput(this.loginForm, input)
        })
      }
      updateHiddenInput(this.loginForm, { value: fromP13N ? 'P13N' : ref, name: 'loginWidgetOrigin', id: 'loginWidgetOrigin' })
    })

    event.on('switchForm', ({ forgot, form }) => {
      form.classList.toggle('hide', forgot)
      focusOnFirstInput(this.activeForm(form))
    })

    event.on('modalClose', () => {
      restore(this.formsContainer, initialPosition)
    })

    document.addEventListener('click', e => {
      if (window.loggedIn) return
      const { target } = e
      const { classList, nodeName } = target
      if (!classList && !(nodeName !== 'A' && nodeName !== 'BUTTON')) {
        return
      }
      const login = classList.contains('login')
      const forgot = classList.contains('forgot')
      const backToLogin = classList.contains('back-to-login')
      if (!login && !forgot && !backToLogin) return
      e.preventDefault()

      const formContainer = target.closest('.login__username') || document.querySelector('.login__username')
      const form = formContainer.querySelector('.login__form')

      let ref = target.dataset.ref
      if (!ref) {
        try {
          ref = target.closest('[data-ref]').dataset.ref
        } catch (err) {
          // debug('No REF for clicked login item', target)
        }
      }

      if (login) event.trigger('login', { ref })
      else event.trigger('switchForm', { forgot, form })
    })
  }
}
