export default {
  init (config, context) {
    const { labels } = window.JSi18n.FE
    var toggleBtn = document.getElementById('tagsToggleBtn')
    var allTagsWrapper = document.getElementById('tagList')
    var tagsInner = document.getElementById('tagsInner')

    ConferenceLinksCalc(tagsInner)

    if (!allTagsWrapper) return
    toggleBtn.addEventListener('click', function (e) {
      allTagsWrapper.classList.toggle('active')
      if (toggleBtn.classList.toggle('arrow__down')) { toggleBtn.innerText = (labels.showMore || 'Show more'); toggleBtn.setAttribute('aria-label', toggleBtn.innerText) }
      if (toggleBtn.classList.toggle('arrow__up')) { toggleBtn.innerText = (labels.showLess || 'Show less'); toggleBtn.setAttribute('aria-label', toggleBtn.innerText) }
    })
  }
}

function ConferenceLinksCalc (tagsInner) {
  var mobile = window.matchMedia('(max-width: 768px)')

  if (!tagsInner) return

  if (mobile.matches) {
    if (tagsInner.clientHeight > 91) {
      document.getElementById('tagsInner').style.maxHeight = '91px'
      document.getElementById('tagsToggleBtn').style.display = 'inline-block'
      document.getElementById('tagsMain').style.maxHeight = '100%'
    } else {
      document.getElementById('tagsToggleBtn').style.display = 'none'
      document.getElementById('tagsMain').style.maxHeight = '100%'
    }
  } else {
    if (tagsInner.clientHeight > 61) {
      document.getElementById('tagsInner').style.maxHeight = '61px'
      document.getElementById('tagsToggleBtn').style.display = 'inline-block'
      document.getElementById('tagsMain').style.maxHeight = '100%'
    } else {
      document.getElementById('tagsToggleBtn').style.display = 'none'
      document.getElementById('tagsMain').style.maxHeight = '100%'
    }
  }
}
