import Popper from 'popper.js'

const popperArrow = '<span class="popper__arrow" x-arrow></span>'

/**
 * Creates a new popper HTML element
 *
 * @param el {HTML Element} - the parent node which hooks the popper
 * @param html {string} - the html it contains
 * @param opts {object} - configuration of the individual popper
 * @returns [HTMLElement] - the created popper
 */
const createPopper = (el, html, opts) => {
  if (!el || !html) {
    throw new Error('Incorrect popper initializaiton')
  }
  let popper
  if (el.classList.contains('popper')) {
    popper = el
  } else {
    popper = document.createElement('DIV')
    popper.classList.add('popper')
  }

  popper.innerHTML = popperArrow + html
  el.append(popper)
  return popper
}

export default {
  get cardsInPage () {
    return document.querySelectorAll('.cards > li')
  },
  init (config, context) {
    let { modules } = config
    const { cardsInPage } = this
    Object.keys(modules).forEach(module => {
      try {
        const m = require(`./poppers/${module}.js`).default
        m.init(modules[module], { Popper, createPopper, cards: cardsInPage }, context)
      } catch (e) {
        throw new Error(`Module "${module}" failed to load. Reason: `, e)
      }
    })
  }
}
