export default {
  init () {
    const searchBars = document.querySelectorAll('form.search')
    if (!searchBars.length) return

    /**
     * Enables / disables the search button on search bars;
     * --
     * Sometimes we may have more searchbars on page (eg. 404)
     * we add input event listeners to every off them
     */
    searchBars.forEach(bar => {
      const inpt = bar.querySelector('input[type="text"]')
      const submit = bar.querySelector('input[type="submit"]')
      if (!inpt || !submit) {
        throw new Error('search bar missing el', { inpt, submit })
      }
      submit.disabled = 'disabled'

      inpt.addEventListener('input', e => {
        let { value } = e.target
        value = String(value).trim()
        if (!value) submit.disabled = 'disabled'
        else submit.removeAttribute('disabled')
      })
    })
  }
}
