export default {
  init (config, { event }) {
    const page = document.querySelector('.privacyPage')
    if (!page) return

    const next = page.querySelector('.next')
    const prev = page.querySelector('.prev')

    const hash = true
    const ids = []
    const instance = page.querySelector('.tabs')
    const tabs = instance.querySelectorAll('.tab')

    for (const tab of tabs) {
      ids.push(tab.dataset.id)
    }

    let activeId = 0

    if (document.location.hash) {
      activeId = ids.indexOf(document.location.hash.replace('#', ''))
    }

    prev.disabled = activeId === 0
    next.disabled = activeId === ids.length - 1

    const changer = page.querySelector('.changer')
    changer.value = activeId

    changer.addEventListener('change', (e) => {
      event.trigger('tabChange', { instance, payload: { index: Number(e.target.value), hash } })
    })

    next.addEventListener('click', e => {
      if (e.target.disabled) return
      event.trigger('tabChange', {
        instance,
        payload: {
          index: activeId + 1,
          hash
        }
      })
    })

    prev.addEventListener('click', e => {
      if (e.target.disabled) return
      event.trigger('tabChange', {
        instance,
        payload: {
          index: activeId - 1,
          hash
        }
      })
    })

    event.on('tabChange', e => {
      if (instance.id !== e.instance.id) return
      activeId = ids.indexOf(document.location.hash.replace('#', ''))
      prev.disabled = activeId === 0
      next.disabled = activeId === ids.length - 1
      changer.value = activeId
      window.scrollTo(0, 0)
    })
  }
}
