export default {
  init (cfg, ctx) {
    const { event } = ctx
    const { labels } = window.JSi18n.FE
    var bookmark = document.getElementById('bookmarkBtn')
    var toastContainer = document.getElementById('toastContainer')
    if (!bookmark) return

    function bookmarkUnbookmark (bookmarkedLabel, buttonSet, buttonUnset) {
      bookmark.classList.add(buttonSet)
      bookmark.classList.remove(buttonUnset)
      var bkmSpan = toastContainer.getElementsByTagName('span')[0]

      bkmSpan.innerText = bookmarkedLabel
      toastContainer.classList.remove('toast-none')
      toastContainer.classList.add('toast-show')
      toastContainer.classList.remove('toast-hide')

      setTimeout(function () {
        toastContainer.classList.remove('toast-show')
        toastContainer.classList.add('toast-hide')
      }, 3000)
    }

    event.on('contentBookmarked', e => {
      var bookmarkedLabel = labels.bookmarked || 'Content Bookmarked'
      bookmarkUnbookmark(bookmarkedLabel, 'button__green', 'button__gray')
    })

    event.on('contentUnbookmarked', e => {
      var unbookmarkedLabel = labels.unbookmarked || 'Content Unbookmarked'
      bookmarkUnbookmark(unbookmarkedLabel, 'button__gray', 'button__green')
    })

    bookmark.addEventListener('click', function (e) {
      event.trigger('bookmarkRequested')
    })
  }
}
