// import { error } from '../debug'
import {
  createFollowable,
  createFollowButton
} from '../helpers/followLike'
import backendGetters from '../helpers/backendGetters'

const { segments, usersTopics } = backendGetters
window.usersTopics = usersTopics

/**
 * Populator function
 *
 * @param segment {string} - 'topic' | 'user'
 * @param selector {HTMLElement} - container to be filled
 * @param placement {string} - various placement scenarios / zones that require dif HTML
 * @param createFromScratch {boolean} - either constructs the entire followable or not
 * @param size {string} - HTML snippet size (mostly refers to buttons) - 'small' | 'medium' | 'large'
 * @param hideButton (boolean) - if the create from scratch pop should have/show a hide button
 * @param asCards (boolean) - creates cards. only used with createFromScratch
 */
function populate ({ segment, selector, placement, createFromScratch, size, hideButton, asCards, isSegmentOfPS }) {
  if (!selector || !selector.length || !segment) {
    throw new Error('Invalid selector / segment provided, aborting.', selector)
  }

  const items = document.querySelectorAll(selector)

  // don't throw
  if (!items || !items.length) return

  // Selector is passed as parent item, filling only children
  if (createFromScratch) {
    const parentItem = items.item(0)

    const { classList } = parentItem

    if (classList && classList.contains(`${segment === 'user' ? 'author' : segment}s`)) {
      const list = parentItem.nodename === 'UL' ? parentItem : parentItem.querySelector('ul')

      /**
       * Describe the cards properties for rendering
       */
      if (asCards) {
        list.classList.add('cards')
        list.classList.add('boxes')

        Object.assign(list.dataset, {
          cols: 2,
          size: 'medium',
          horizontal: true,
          tax: 'news'
        })
      }
      if (!list) {
        throw new Error('Missing UL item to populate on')
      }
      for (const item of list.children) {
        let { id, ref } = item.dataset
        if (item.innerHTML) continue
        if (id || ref) {
          if (!id) id = ref
          const data = backendGetters[segment](ref || id)
          if (!data) {
            throw new Error('data missing for id', id)
          }
          createFollowable({
            segment,
            item,
            data,
            hideButton,
            asCards,
            isSegmentOfPS
          })
        }
      }
    }
  } else {
    for (const item of items) {
      let { id, ref } = item.dataset
      if (id || ref) {
        let flag
        // for users :S
        if (!id) id = ref
        const data = backendGetters[segment](ref || id)

        if (!data) {
          // console.info(`Data for ${segment} ${id} missing in var`)
          continue
        }

        flag = data.followedByCurrentUser
        if (flag === undefined) continue

        const button = createFollowButton({ flag, size, segment })

        switch (placement) {
          default:
            item.append(button)
            break

          case 'topicPageHeader':
            const actionsRight = document.createElement('DIV')
            actionsRight.classList.add('actions__right')
            actionsRight.append(button)
            item.append(actionsRight)
            break
        }
      }
    }
  }
}

export default {
  /**
   * Init function - Module constructor
   *
   * @param {object} module config
   * @param {object} passed context to module
   */
  init (config, context) {
    const { api, event } = context

    // helper so we don't call twice the api
    let requested = false
    let itemData

    event.on('renderEditorPicks', ({ element, data }) => {
      segments.forEach(seg => {
        const segment = `${seg}s`
        const what = seg === 'user' ? 'authors' : 'topics'

        const container = document.createElement('DIV')
        container.classList.add(what)

        const heading = document.createElement('H3')
        heading.classList.add('heading')
        heading.textContent = window.JSi18n.FE.labels[segment] || segment
        container.append(heading)

        const ul = document.createElement('UL')
        ul.classList.add('no-style')
        ul.classList.add('ul__border')

        data[segment].forEach(item => {
          if (!item.id) {
            throw new Error('invalid item .id supplied for editor picks', item)
          }
          const li = document.createElement('LI')
          li.dataset.id = item.ref || item.id
          ul.append(li)
        })
        container.append(ul)
        element.append(container)

        const elS = element.classList.toString().replace(' ', '.')
        const selector = `.${elS} .${what}`

        populate({
          segment: seg,
          isSegmentOfPS: true,
          createFromScratch: true,
          selector
        })
      })

      // element.classList.remove('preload')
    })

    event.on('getEditorPicks', async e => {
      const { element } = e
      if (!element) return

      event.on('gotPicksData', ({ data }) => {
        event.trigger('renderEditorPicks', { element, data })
      })

      // element.classList.add('preload')

      if (requested) return
      requested = true
      element.classList.add('unique')

      try {
        const { data } = await api.get(`/userProfile!getAllPicks.action`)

        // parse and adapt results :S
        data.forEach(result => {
          result.followers = Number(result.count)
          result.name = result.title
          result.url = result.link
          delete result.count
        })
        let users = data.filter(res => res.type === 'fu')

        users.forEach(user => {
          const { CommonUtils: { encodeAuthorName } } = window
          if (!encodeAuthorName || typeof encodeAuthorName !== 'function') return
          user.ref = `author-${encodeAuthorName(user.title)}`
        })

        itemData = {
          users,
          topics: data.filter(res => res.type === 'ft')
        }

        // assign new results, make reactive
        segments.forEach(seg => {
          const segment = `${seg}s`
          if (!itemData[segment] || !itemData[segment].length) return
          usersTopics[segment] = itemData[segment]
        })

        event.trigger('gotPicksData', { data: itemData })
      } catch (err) {
        throw new Error(err)
      }
    })
  }
}

export { populate }
