export default {
  init (config, context) {
    const { Popper, createPopper } = context

    const schedules = document.querySelectorAll('.easchedule > div.video-block')

    if (!schedules.length) {
      // debug('no schedules found')
      return
    }

    for (const schedule of schedules) {
      let pop

      schedule.addEventListener('mouseenter', () => {
        pop = new Popper(
          schedule,
          createPopper(
            schedule,
            `<p>${schedule.dataset.tooltip || 'Not published due to author permission'}</p>`,
            config
          )
          , config
        )
      })

      schedule.addEventListener('mouseleave', () => {
        if (!pop) return
        pop.destroy()
      })
    }
  }
}
