let closeEls = []

export default {
  init ({ elements }, { event }) {
    if (!elements) {
      return
    }
    closeEls = elements

    event.on('closed', e => {
      if (!e.closable) return
      e.closable.remove()
    })

    document.addEventListener('click', e => {
      const { nodeName, classList } = e.target
      if (nodeName !== 'BUTTON' || !classList.contains('close')) {
        return
      }
      let { closeables } = this
      for (const closable of closeables) {
        if (closable.contains(e.target)) {
          event.trigger('closed', { closable })
        }
      }
    })
  },

  get closeables () {
    return closeEls ? document.querySelectorAll(closeEls) : null
  }
}
