import backendGetters from '../helpers/backendGetters'
import tabs from '../modules/tabs'
import { hideItemHandler, createFollowButton } from '../helpers/followLike'

const { makeTabs } = tabs
const { constants, usersTopics, user } = backendGetters
const { token, uidToShow, isFollowedByCurrentUser } = constants
const { labels } = window.JSi18n.FE

const allActivityKey = 'allActivity'

// main object, used to cache data
const dataObj = Object.create(null)

// TODO: put/export these in config
const allKeys = ['fu', 'f', 'ft', 'lc', 'cml', 'cmo', 'news', 'articles', 'presentations', 'minibooks', 'podcasts', 'interviews', 'research', 'reviewed']
const activityIndexes = {}

// initial indexes
allKeys.map(key => { activityIndexes[key] = 0 })
let allActivityFirstRequest = false

/**
 * Gets the indexes for all activity items
 * @param {array} data - data received from api
 */
const getAllActivityIndexes = (data) => {
  if (!data) return activityIndexes
  allKeys.map(key => {
    activityIndexes[key] += data && data.length
      ? data.filter(item => item.type === key).length
      : 0
  })
  return activityIndexes
}

export default {
  init (config, context) {
    const profilePage = document.querySelector('main > .profile')
    if (!profilePage) return

    const profileBottom = profilePage.querySelector('.profile__bottom')
    if (!profileBottom) return

    const profileTabs = profileBottom.querySelector('.tabs')
    const { event, api } = context
    const asCards = true

    event.on('setItemsIndex', e => {
      const { segment, newIndex } = e
      // debug(e)

      switch (segment) {
        default:
          dataObj[segment]._index = newIndex
          break

        case 'topic':
        case 'user':
          dataObj.following._taxonomies[`${segment}s`]._index = newIndex
          break

        case 'allActivity':
          dataObj[allActivityKey]._index = newIndex
          break
      }
    })

    // handle main profile navigation
    event.on('tabChange', e => {
      const { payload: { instanceId } } = e
      /** This is for the html element and not for pagination */
      const tabElIndex = e.payload.index

      let index = 0
      if (['profileTabs', 'following'].indexOf(instanceId) < 0) return

      const elements = e.instance.querySelectorAll(':scope > .tab')

      switch (instanceId) {
        case 'profileTabs':
          const item = elements.item(tabElIndex)
          if (!item) break

          const id = item.id || item.dataset.id
          let element = item.querySelector(':scope > div')
          element.classList.add(id)

          switch (id) {
            default:
              const itemIndex = dataObj[id]._index || 0
              if (index <= itemIndex) break

              const stuff = {
                segment: id,
                element,
                index,
                asCards
              }
              if (id === 'followers') stuff.selector = `.profile__bottom .${id}`
              event.trigger('getStuffYouFollow', stuff)
              break

            case 'following':
              const instance = document.querySelector('.tab[data-id="following"] .tabs')
              if (!instance) break
              event.trigger('tabChange', {
                instance,
                payload: {
                  instanceId: id,
                  index: 0
                }
              })
              break

            case 'allActivity':
              const indexes = getAllActivityIndexes()
              let shouldRequest = false
              let allIndexesAreZero = true
              let indexHasChanged = false
              Object.keys(indexes).forEach(tax => {
                const i = Number(indexes[tax])
                if (activityIndexes[tax] < i) indexHasChanged = true
                if (i > 0) allIndexesAreZero = false
              })
              if (!allIndexesAreZero && indexHasChanged) shouldRequest = true
              if (allIndexesAreZero && !indexHasChanged && !allActivityFirstRequest) shouldRequest = true

              if (!shouldRequest) break
              event.trigger('getStuffYouFollow', {
                segment: id,
                element,
                single: false,
                asCards,
                otherKeys: indexes
              })
              allActivityFirstRequest = true
              break
          }
          break

        case 'following':
          const tabId = elements.item(tabElIndex).dataset.id

          if (!tabId) break
          const segment = tabId

          const itemIndex = dataObj[instanceId]._taxonomies[`${segment}`]._index
          element = elements.item(tabElIndex)

          if (index <= itemIndex) break
          event.trigger('getStuffYouFollow', {
            segment: `${segment}`.replace('rs', 'r').replace('cs', 'c'),
            element,
            index,
            asCards
          })
          break
      }
    })

    event.on('getCounts', async (e) => {
      await this.populateDataObj({ api, e })
      this.makeTabs(profileTabs, { hash: true }, event)

      const switcher = profileTabs.childNodes[0]
      const heading = document.createElement('h3')
      heading.classList.add('section__heading', 'heading')
      heading.textContent = labels['allActivity']
      switcher.prepend(heading)

      profileTabs.classList.remove('preload')

      const actions = document.querySelector('.profile .profile__bio .actions__right')
      if (actions) {
        const id = uidToShow
        usersTopics['users'] = [{
          id,
          type: 'fu',
          followedByCurrentUser: isFollowedByCurrentUser
        }]
        actions.dataset.id = id

        if (uidToShow !== window.currentUserId) {
          const followButton = createFollowButton({
            segment: 'user',
            size: 'medium',
            flag: user(id).followedByCurrentUser
          })
          actions.parentNode.classList.add('authors')
          actions.append(followButton)
        }
      }

      const stats = document.querySelector('.profile__stats')
      if (stats) {
        const flers = document.createElement('LI')
        const fling = document.createElement('LI')

        const { followers, following } = dataObj
        flers.innerHTML = followers ? `
          <span class="_followers meta">${Number(followers._count) > 0 ? `${followers._count} ${labels.followers}` : ''}</span>
        ` : ''
        fling.innerHTML = following ? `
          <span class="_following meta">${Number(following._count) > 0 ? `${following._count} ${labels.following}` : ''}</span>
        ` : ''
        if (flers.innerHTML) stats.prepend(flers)
        if (fling.innerHTML) stats.prepend(fling)
      }
    })

    event.trigger('getCounts')
    hideItemHandler(event)
  },

  /**
   * Constructs the tabs
   *
   * @param {HTMLElement} parentHolder
   */
  makeTabs (parentHolder, config, ctx) {
    if (!parentHolder) return

    Object.keys(dataObj)
      .sort((a, b) => dataObj[a]._order - dataObj[b]._order)
      .forEach(tab => {
        const { _count, _label, _taxonomies } = dataObj[tab]
        if (!_count && tab !== allActivityKey) return

        const tabEl = document.createElement('DIV')
        tabEl.classList.add('tab')
        Object.assign(tabEl.dataset, {
          title: _label,
          count: _count && _count !== undefined ? _count : null,
          id: tab
        })

        tabEl.innerHTML = `
          <h2 class="heading">${_label}</h2>
          <div class="preload"></div>
        `

        // For 'following' only atm
        if (_taxonomies) {
          const taxTabs = tabEl.querySelector('.preload')

          // ie10, 1 by 1 :(
          taxTabs.classList.add('tabs')
          taxTabs.classList.add('tabs__taxonomies')
          taxTabs.classList.remove('preload')
          taxTabs.dataset.id = 'following'

          Object.keys(_taxonomies).forEach(taxonomy => {
            const { _count, _label } = _taxonomies[taxonomy]
            if (!_count) return
            const taxTabEl = document.createElement('DIV')
            taxTabEl.classList.add('tab')
            taxTabEl.classList.add(`${taxonomy.replace('_', '').replace('users', 'peers')}-you-follow`)

            Object.assign(taxTabEl.dataset, {
              title: _label,
              count: _count && _count !== undefined ? _count : null,
              id: taxonomy
            })
            taxTabEl.innerHTML = `<ul class="no-style preload"></ul>`
            taxTabs.append(taxTabEl)
          })
          tabEl.append(taxTabs)
          makeTabs(taxTabs, {}, ctx)
        }

        parentHolder.append(tabEl)
      })
    parentHolder.id = 'profileTabs'
    makeTabs(parentHolder, config, ctx)
  },

  /**
   * Popualates the object element for references
   */
  async populateDataObj (context) {
    const { api } = context
    try {
      const { data } = await api.get('/userProfile!getCounts.action', {
        params: {
          token
        }
      })

      /**
       * Assign all activity key
       */
      Object.assign(dataObj, {
        [allActivityKey]: {
          _label: labels[allActivityKey]
        }
      })

      /**
       * Used to make separate tabs for users and topics
       */
      // const keysWithTaxonomies = ['following']

      Object.keys(data).forEach(key => {
        if (key.indexOf('owned') < 0 && key.indexOf('All') < 0 && key.indexOf('followers')) return
        const tax = String(key
          .replace('owned', '')
          .replace('All', '')
          .replace('ked', 'kes') // liked -> likes
          .replace('owed', 'owing') // followed -> following
        ).toLowerCase()

        /**
         * Custom ordering for tab elements
         */
        const _order = (tax => {
          switch (tax) {
            case allActivityKey:
            default:
              return 0

            case 'articles': return 1
            case 'news': return 2
            case 'research': return 3
            case 'presentations': return 4
            case 'interviews': return 5
            case 'minibooks': return 6
            case 'podcasts': return 7
            case 'reviewed': return 8

            case 'likes': return 10
            case 'comments': return 11

            case 'following': return 20
            case 'followers': return 21
          }
        })(tax)

        Object.assign(dataObj, {
          [tax]: {
            _count: data[key],
            _label: labels[tax],
            _order,
            _index: -1 // -1 = not inited. it's 0 when first requested
          }
        })

        if (key.indexOf('followedAll') > -1) {
          const { followedUsers, followedTopics } = data
          Object.assign(dataObj[tax], {
            _taxonomies: {
              users: {
                _count: followedUsers,
                _label: labels['people'],
                _index: -1
              },
              topics: {
                _count: followedTopics,
                _label: labels['topics'],
                _index: -1
              }
            }
          })
        }
      })

    } catch (e) {
      throw new Error(e)
    }
  }
}

export { allActivityKey, allKeys, getAllActivityIndexes }
