// import Imagezoom from 'image-zoom'
const { googletag } = window

/**
 * Presentation page function helpers // TODO: export separately
 */
const bannerId = 'id_300x250_banner_top'

/**
 * Re-renders the ad, necessary when moving the parent container or the el itself
 * (presentation page)
 * @param {HTMLElement} ad - the ad element to be rerendered
 */
function reRenderAd (ad) {
  if (googletag === undefined || !ad) return
  const { parentNode } = ad
  if (!parentNode) {
    throw new Error('Invalid ad element supplied')
  }
  const newAd = document.createElement('DIV')
  ad.remove()
  newAd.classList.add('ad')
  newAd.classList.add('ad__300x250')
  newAd.id = bannerId
  newAd.dataset.place = 'TOP'
  const newScript = document.createElement('script')
  const wrappedScriptContent = `googletag.cmd.push(function () { googletag.display("${bannerId}"); });`

  // create a blob from the wrapped script content
  const scriptBlob = new Blob([wrappedScriptContent], { type: 'text/javascript' })
  newScript.src = URL.createObjectURL(scriptBlob)
  newAd.append(newScript)
  parentNode.append(newAd)
}

/**
 * Handles containers on presentation page when layout changes
 *
 * @param {HTMLElement} article -> containing article
 * @param {HTMLElement[]} els -> affected elements getters
 * @param {boolean} putBack -> restore initial position or not
 * @param {boolean} hasNotes -> for presentation with notes
 */
function moveContainers (article, els, putBack, hasNotes) {
  const { header, ad, about, summary, sponsored, intro, bio, middleCol, leftCol, headerWidgets, full } = els
  const columns = header.children[0]
  // const left = columns.children[0]
  const right = columns.children[1]

  if (!putBack) {
    right.appendChild(summary)
    right.appendChild(about)
    full.prepend(intro)
    if (sponsored) leftCol.append(sponsored)
    if (ad) leftCol.append(ad)
    if (hasNotes) middleCol.prepend(bio)
  } else {
    if (hasNotes) {
      headerWidgets.prepend(bio)
      headerWidgets.prepend(about)
      if (sponsored) headerWidgets.append(sponsored)
      if (ad) headerWidgets.append(ad)
    } else {
      middleCol.prepend(about)
      if (sponsored) headerWidgets.append(sponsored)
      if (ad) headerWidgets.append(ad)
    }
    headerWidgets.prepend(summary)
    right.prepend(intro)
  }
}

/**
* Flex sucks and this is the reason we have this
* adjusts the content haeight based on the sidebar's height
* name may be confusing but makes more sense when looking it up
*
* @param {object} els  -> reference items to do the maths on
*/
function adjustPresentationSidebarHeight (els) {
  const { headerWidgets, player, header, content, article } = els
  if (!article.dataset) return
  if (article.dataset.layout === 'pres-1-col') {
    content.style.minHeight = 'auto'
    return
  }
  const sidebarHeight = player.offsetHeight + headerWidgets.offsetHeight
  const contentHeight = header.offsetHeight + content.offsetHeight
  const dif = sidebarHeight - contentHeight - player.offsetHeight
  content.style.minHeight = `${dif + content.offsetHeight}px`
}

export default {
  /**
   * An active getter for all the dynamic elements that may change
   */
  get presEls () {
    const { article } = this
    return {
      controller: article.querySelector('.presentation__controller'),
      header: article.querySelector('.presentation__header'),
      sponsored: article.querySelector('.article__metadata .sponsored'),
      ad: article.querySelector('.article__metadata .ad__300x250'),
      headerWidgets: article.querySelector('.presentation__header .widgets'),
      about: article.querySelector('.presentation__about'),
      player: article.querySelector('.player'),
      summary: article.querySelector('.summary'),
      intro: article.querySelector('.intro'),
      full: article.querySelector('.full'),
      bio: article.querySelector('.author__bio'),
      content: article.querySelector('.article__data'),
      sep: article.querySelector('.separator'),
      leftCol: article.querySelector('.article__explore .article__metadata'),
      widgets: article.querySelector('.article__explore .article__metadata .widgets'),
      middleCol: article.querySelector('.article__content')
    }
  },

  /**
   * This only gets inited on pages that have an <article>
   *
   * @param {*} cfg
   * @param {*} ctx
   */
  init (cfg, ctx) {
    const { event, modal } = ctx
    const article = document.querySelector('article.article')
    if (!article) { return }
    this.limitAuthorsCount(cfg.limitAuthors)

    // pres stuff
    const pswitch = document.querySelector('.presentation__switch')
    if (pswitch) {
      this.article = article

      // move widgets on pres 2 cols
      const { widgets, header, content, headerWidgets, player } = this.presEls

      // hacky...
      setInterval(() => adjustPresentationSidebarHeight({ article, headerWidgets, content, player, header }), 1000)
      const headerLeftCol = header.querySelector('.article__metadata')

      if (widgets && headerLeftCol) {
        headerLeftCol.append(widgets)
      }

      const demoClass = 'presentation--demo'
      event.on('demoIn', e => {
        article.classList.add(demoClass)
      })

      event.on('demoOut', e => {
        article.classList.remove(demoClass)
      })
    }

    // Presentation Notes
    const notes = document.getElementById('presentationNotes')
    if (notes) this.initPresentationNotes(notes, { wrap: true })

    // opens the 'bundle' modal
    event.on('download', e => {
      const { targetType, target, click } = e

      if (targetType !== 'modal' || !target) return

      const targetEl = document.getElementById(target)
      if (!targetEl) return
      click.preventDefault()
      modal(targetEl)
    })

    /**
     * VCR Article / Page DOWNLOAD functionality
     */
    const vcrArticle = document.querySelector('article[data-type="rvc"]')
    if (vcrArticle) {
      const signInIndicator = 'signin--displayed'
      const signUpIndicator = 'signup--displayed'

      // VCR page custom func
      event.on('downloadVcr', e => {
        if (window.loggedIn || vcrArticle.classList.contains(signInIndicator)) return
        const vcrSignInContainer = vcrArticle.querySelector(`.article__footer > div`)
        if (!vcrSignInContainer) {
          throw new Error('Missing VCR signin section in page')
        }
        vcrArticle.classList.add(signInIndicator)
        try {
          document.getElementById('authenticate').scrollIntoView()
        } catch (e) {
          throw new Error(`Could not scroll Element into view: ${{ ...e }}`)
        }
      })

      event.on('signupInsideVcr', e => {
        vcrArticle.classList.remove(signInIndicator)
        vcrArticle.classList.add(signUpIndicator)
      })

      event.on('cancelSignupInsideVcr', e => {
        vcrArticle.classList.remove(signUpIndicator)
      })

      const dlButton = vcrArticle.querySelector('.download--vcr')
      const signUpButton = vcrArticle.querySelector('.login__signup > .button')
      const cancelSignupButton = vcrArticle.querySelector('.signup .cancel')
      if (dlButton) {
        dlButton.addEventListener('click', e => {
          event.trigger('downloadVcr')
        })
      }

      if (signUpButton) {
        signUpButton.addEventListener('click', e => {
          e.preventDefault(true)
          event.trigger('signupInsideVcr')
        })
      }
      if (cancelSignupButton) {
        cancelSignupButton.addEventListener('click', e => {
          e.preventDefault()
          event.trigger('cancelSignupInsideVcr')
        })
      }
    }

    // share with friend tooltip close handle
    const swfContainer = article.querySelector('.swf__container')
    if (swfContainer) {
      const swfHandle = swfContainer.querySelector('.icon__help')
      let closed = false
      let clone
      let closable = swfContainer.querySelector('.swf__tooltip')

      event.on('closed', e => {
        if (!swfHandle || !e.closable || clone) return
        const { closable: { classList } } = e
        if (!classList || !classList.contains('swf__tooltip')) return
        closable = e.closable
        clone = e.closable.cloneNode(1)
        if (!clone) return
        closed = true
      })

      swfHandle.addEventListener('mouseenter', () => {
        if (closable) closable.style.display = 'flex'
        if (!closed || !clone) return
        clone.style.display = 'flex'
        swfContainer.prepend(clone)
      })
    }

    document.addEventListener('click', e => {
      const { target } = e
      this.handleDownloadButtons(target, ctx.event, article, e)
      this.handlePrint(target)
      // this.zoomImages(e, articleContent)

      if (pswitch) this.handlePresentationSwitch(target, article, pswitch, this.presEls, notes)
      if (notes) {
        this.handlePresentationNotes(target)
        this.expandPresNotes(target, notes)
      }
    })
  },

  /**
   *
   * @param {HTMLElement} t - target
   * @param {Event} e - event
   * @param {HTMLElement} article - article
   * @param {Event} click event
   */
  handleDownloadButtons (t, e, article, click) {
    const actions = t.parentNode.parentNode

    if (['BUTTON', 'INPUT'].indexOf(t.nodeName) < 0) return
    if (t.nodeName === 'BUTTON' && (!actions || !actions.classList.contains('download'))) return
    if (t.nodeName === 'INPUT' && (t.type !== 'submit' || !t.classList.contains('download'))) return

    if (!window.loggedIn) {
      click.preventDefault()
      let ref = t.dataset.ref
      if (!ref) {
        try {
          ref = t.closest('[data-ref]').dataset.ref
        } catch (e) {
          // debug('No REF for clicked login item', t)
        }
      }
      e.trigger('login', { ref })
      return
    }
    // click.preventDefault()
    const { target } = t.dataset
    if (target) {
      click.preventDefault()
    }
    e.trigger('download', {
      contentType: article.dataset.type,
      targetType: t.dataset.type,
      target,
      click
    })
  },

  expandPresNotes (t, notes) {
    if (t.nodeName !== 'BUTTON' || !t.classList.contains('more')) return
    notes.classList.add('full')
    t.remove()
  },

  /**
   * Toggles the layout of a presentation
   * based on the switch
   *
   * @param {*} t - target, clicked element of pswitch
   * @param {*} article
   * @param {*} pswitch
   * @param {*} els
   * @param {*} hasNotes
   */
  handlePresentationSwitch (t, article, pswitch, els, hasNotes) {
    const { key } = t.dataset
    if (!key) return
    if (t.classList.contains('active')) return

    // button status
    const fs = t.dataset.key === 'pres-fs'
    if (fs) {
      const requestFullScreen = article.requestFullscreen || article.mozRequestFullScreen || article.webkitRequestFullScreen || article.msRequestFullscreen
      const cancelFullScreen = document.exitFullscreen || document.mozCancelFullScreen || document.webkitExitFullscreen || document.msExitFullscreen

      if (!document.fullscreenElement && !document.mozFullScreenElement && !document.webkitFullscreenElement && !document.msFullscreenElement) {
        requestFullScreen.call(article)
      } else {
        cancelFullScreen.call(document)
      }
      return
    }

    for (const el of pswitch.children) {
      const but = el.children[0]
      if (but.nodeName !== 'BUTTON') continue
      if (but.dataset.key === 'pres-2-cols') {
        but.click()
      }
      but.classList.remove('active')
    }

    t.classList.add('active')

    article.dataset.layout = key
    moveContainers(article, els, key === 'pres-2-cols', hasNotes)
    reRenderAd(els.ad)
  },

  handlePresentationNotes (target) {
    const id = 'presentationNotes'
    if (target.nodeName !== 'H2' || target.parentNode.id !== id) return
    target.classList.toggle('expanded')
  },

  /**
   * Re-adjusts the markup for presentation pages with notes
   *
   * @param {HTMLElement} notes
   * @param {any} config
   */
  initPresentationNotes (notes, config) {
    const h2s = notes.querySelectorAll('h2')

    // skip for short articles
    if (!h2s || h2s.length < 2) return

    // const showMore = document.createElement('button')
    // showMore.textContent = labels.showMore || 'Show More'
    // showMore.classList.add('button', 'more')
    const toDiv = (vector, index) => {
      const wrapper = document.createElement('DIV')
      wrapper.classList.add('notesWrapper')
      for (const nod of vector) wrapper.append(nod)
      notes.insertBefore(wrapper, h2s[index])
    }
    if (config.wrap) {
      let child = notes.firstChild

      let arr = []
      let i = 0

      while (child) {
        if (child.nodeName === 'H2') {
          if (arr.length) toDiv(arr, i)
          arr = []
          i++
        } else {
          arr.push(child)
          if (child === notes.lastChild) toDiv(arr, i)
        }
        child = child.nextSibling
      }
    }
    // notes.append(showMore)
  },

  // initZoomableImages (area) {
  //   const images = area.querySelectorAll('[rel="expand"]')
  //   if (images.length < 1) {
  //     // debug('No images found in article')
  //     return
  //   }
  //   for (const image of images) {
  //     image.dataset.zoomUrl = image.src
  //     image.zoom = new Imagezoom(image).overlay()
  //   }
  // },

  // zoomImages (e, content) {
  //   const { target } = e
  //   const { nodeName, zoom } = target

  //   if (nodeName !== 'IMG' || !content.contains(target)) {
  //     return
  //   }
  //   e.stopPropagation()
  //   if (zoom) zoom.show()
  // },

  /**
   * Prints the article
   * @param {*} target
   */
  handlePrint (target) {
    const { nodeName, classList } = target
    if (nodeName !== 'BUTTON' || !classList.contains('icon__print')) {
      return
    }
    window.print()
  },

  /**
   * Limits the author count on content pages
   */
  limitAuthorsCount (opts) {
    const container = document.querySelector('.article__authors')
    if (!container) return

    const authors = container.querySelectorAll('li[data-id]')
    const { length } = authors
    if (!length) return
    const { count } = opts
    const limit = count + 1
    if (length <= limit) return

    const others = length - limit

    // sections such as 'reviewd by', 'translated by'
    const sections = container.children
    const { labels } = window.JSi18n.FE

    const hideAuthors = () => {
      let i = -1
      for (const author of authors) {
        i += 1
        if (i < limit) continue
        author.classList.add('hidden')
      }

      let j = -1
      let count = 0
      // a section holder array, holds the number of authors of each section
      for (const section of sections) {
        if (section.nodeName === 'BUTTON') continue
        j += 1
        const sectionCount = Number(section.querySelectorAll('li[data-id]').length)
        count += sectionCount
        if (count - limit >= sectionCount && j) section.classList.add('hidden')
      }
    }

    const _labels = {
      showMore: String(labels.moreAuthors || `+ %s more others`).replace('%s', others),
      showLess: labels.showLess || 'Show less'
    }

    hideAuthors()

    // show more / less button
    const but = document.createElement('button')
    but.classList.add('button__unstyled')
    but.textContent = _labels.showMore
    but.addEventListener('click', e => {
      const hiddens = container.querySelectorAll('.hidden')
      if (but.classList.contains('less')) {
        hideAuthors()
        but.textContent = _labels.showMore
      } else {
        for (const hidden of hiddens) {
          hidden.classList.remove('hidden')
        }
        but.textContent = _labels.showLess
      }

      but.classList.toggle('less')
    })
    container.append(but)
  }
}
