export default {
  reorderPodcasts (list) {
    const items = list.children
    const maxItems = list.dataset.max

    const positions = []

    for (let i = 0; i < items.length; i++) {
      const item = items.item(i)
      if (!item.classList.contains('podcasts')) continue
      positions.push(i)
    }

    if (!positions.length) {
      return
    }

    const last = items.length < maxItems
      ? items.length
      : maxItems

    // if the very first podcast is not shown
    if (positions[0] >= last) {
      list.insertBefore(list.children[positions[0]], list.children[last - 1])
    }

    list.__podcastsAdjusted = true
  },

  init (config, { event }) {
    const box = document.querySelector('.widget.popular')
    if (!box) return

    const { labels } = window.JSi18n.FE

    const column = box.parentNode
    const { reorderPodcasts } = this
    let moreToggled = false

    this.lists = box.querySelectorAll('.tab ol')
    this.newsSection = column.parentNode.querySelector('[data-col="2/3"] .items')
    this.config = config
    this.windowWidth = window.innerWidth

    const moreLess = document.createElement('BUTTON')
    moreLess.textContent = labels.showMore
    moreLess.classList.add('more')
    moreLess.classList.add('button__medium')

    moreLess.reset = () => {
      moreLess.textContent = labels.showMore
      moreToggled = false
    }

    box.appendChild(moreLess)

    event.on('tabChanged', ({ instance, tab }) => {
      if (instance.id !== 'trending') {
        return
      }
      moreLess.reset()
      if (window.innerWidth < 768) {
        for (const list of this.lists) {
          list.dataset.max = config.defaultToShow
        }
      }
      // tab.classList.toggle('hasMore', list.classList.contains('hasMore'))
    })

    moreLess.addEventListener('click', () => {
      const activeList = box.querySelector('.tab.active ol')
      if (!moreToggled) {
        activeList.dataset.max = 12
        moreLess.textContent = labels.showLess
        moreToggled = true
      } else {
        activeList.dataset.max = config.defaultToShow
        moreLess.reset()
      }
    })

    for (const list of this.lists) {
      list.dataset.max = config.defaultToShow || 5

      Object.defineProperty(list, '__canShow', {
        get () {
          return list.dataset.max
        },

        set (n) {
          if (n < config.defaultToShow) {
            return
          }

          list.dataset.max = n
          if (!list.__podcastsAdjusted) {
            reorderPodcasts(list)
          }
        }
      })
    }

    this.adjustItems()
    window.addEventListener('resize', this.readjust.bind(this))
  },

  readjust () {
    if (window.innerWidth === this.windowWidth) return
    this.adjustItems()
  },

  adjustItems () {
    for (const list of this.lists) {
      list.__canShow = window.innerWidth >= 768
        ? this.calculateHeight(list)
        : this.config.defaultToShow
      list.__podcastsAdjusted = false
      list.classList.toggle('hasMore', list.__canShow < list.children.length)
    }
    if (!this.inited) {
      this.inited = true
    }
  },

  calculateHeight (list) {
    const newsHeight = this.newsSection.offsetHeight
    let h = 0
    let items = 0

    list.style.visibility = 'visible'
    list.style.display = 'block'
    list.parentNode.style.display = 'block'

    for (const li of list.children) {
      if (h > newsHeight - 190) {
        break
      }

      // to get the height accurately
      li.style.display = 'block'
      const { offsetHeight } = li
      li.style = null

      if (!offsetHeight || offsetHeight < 20) {
        continue
      }

      h += offsetHeight
      items += 1
    }

    list.parentNode.style = null
    list.style = null

    return items
  }
}
