import backendGetters from '../../helpers/backendGetters'
import { popperHTML } from '../../helpers/followLike'
const { topic } = backendGetters

export default {
  get el () {
    return document.querySelector('.related__topics')
  },
  init (config, { Popper, createPopper }) {
    const { el } = this
    if (!el) {
      return
    }
    for (const tpc of el.children) {
      const { id } = tpc.dataset
      if (id) {
        const topicData = topic(id)
        if (topicData) {
          const pop = new Popper(tpc.children[0], createPopper(tpc, popperHTML('topic', topicData)), config)
          if (pop) {}
        }
      }
    }
  }
}
