const { allCountries, gdprCountriesIds } = window
let { userCountryId } = window

let clientIsGDPR = Boolean(userCountryId && gdprCountriesIds.indexOf(Number(userCountryId)) > -1)

/**
 * Show questions' fields
 *
 * @param {HTMLElement} form: { questions }
 */
function showQuestions ({ questions }) {
  // if (!qsHidden(questions)) return
  questions.forEach(q => q.classList.remove('hidden'))
  updateQsRequiredState(questions, clientIsGDPR)
}

/**
 * Hide questions' fields
 *
 * @param {HTMLElement} form: { questions }
 */
function hideQuestions ({ questions }) {
  if (qsHidden(questions)) return
  questions.forEach(q => q.classList.add('hidden'))
  updateQsRequiredState(questions, clientIsGDPR)
}

/**
 * Checks if the form is a NS instance or not
 *
 * @param {Form} form
 * @returns {Boolean}
 */
function isNewsletterSubscribe (form) {
  return form.parentNode.classList.contains('newsletter__subscribe')
}

/**
 * Updates the submit button state when required
 *
 * @param {HTMLElement} form
 * @param {Boolean} isNS
 */
function updateSubmitState (form, isNS) {
  const { select, ccbox, email, submit } = form

  submit.disabled = (email && !email.value) ||
    (select && !select.value) ||
    (isNS && clientIsGDPR && !ccbox.checked)
}

/**
 * Checks if questions are hidden
 *
 * @param {HTMLElement} qs
 * @returns {Boolean}
 */
function qsHidden (qs) {
  if (!qs || !qs.length) return
  return qs[0].classList.contains('hidden')
}

/**
 * Updates a form's questions' markup based on GDPR status
 *
 * @param {HTMLElement[]} questions
 * @param {Boolean} status
 */
function updateQsRequiredState (questions, status) {
  questions.forEach(q => {
    const field = q.querySelector('.field')
    const span = q.querySelector('.input__req')
    field.setAttribute('aria-required', !status)

    if (status) {
      if (span) span.remove()
    } else {
      if (span) return
      const req = document.createElement('SPAN')
      req.classList.add('input__req')
      req.textContent = '*'

      const label = q.querySelector('label')
      if (label) label.append(req)
    }
  })
}

/**
 * Updates elements marked with hidden class
 * and adds fieldset gdpr stauts class
 *
 * @param {Form} { ccbox, fieldset }
 */
function updateHiddens ({ ccbox, fieldset, hiddenC }) {
  if (fieldset) fieldset.classList.toggle('is', clientIsGDPR)
  if (!hiddenC) return
  hiddenC.classList.toggle('hidden', !clientIsGDPR)
}

export default {
  init (config, { event }) {
    const formsWgdpr = document.querySelectorAll('form.gdpr')

    for (const form of formsWgdpr) {
      let _hidden, fieldset, hiddenC
      const isNS = isNewsletterSubscribe(form)

      const email = form.querySelector('.email input[type="text"]')
      const select = form.querySelector('.country select')
      const submit = form.querySelector('input[type="submit"]')
      const ccbox = form.querySelector(`input[id^='gdpr']`)
      const _els = { email, select, submit, ccbox }

      const questions = form.querySelectorAll('.question')

      updateSubmitState(_els, isNS)

      if (questions.length) {
        showQuestions({ questions })
      }

      if (isNS) {
        _hidden = form.querySelector('.hidden')
        if (!_hidden) continue
        _hidden.classList.add('inited')
      } else {
        fieldset = form.querySelector('.fieldset__gdpr')

        /**
         * Update modal status when coming after auth to DL
         */
        event.on('modalOpen', ({ parentNode }) => {
          if (!parentNode) return
          const { id } = parentNode
          if (!id || id !== 'vcrQuestions') return

          updateHiddens({ ccbox, fieldset, hiddenC })
          updateQsRequiredState(questions, clientIsGDPR)
          if (clientIsGDPR) hideQuestions({ questions })
        })
      }

      if (email) {
        email.addEventListener('focus', () => {
          if (!_hidden) return
          _hidden.classList.remove('hidden')
        })

        email.addEventListener('input', e => {
          updateSubmitState(_els, isNS)
        })
      }

      if (ccbox) {
        hiddenC = ccbox.closest('.hidden')
        ccbox.addEventListener('change', e => {
          if (isNS) updateSubmitState(_els, isNS)
          if (clientIsGDPR) {
            if (e.target.checked) showQuestions({ questions })
            else hideQuestions({ questions })
          }
        })
      }

      if (select) {
        const { value } = select

        select.addEventListener('change', e => {
          const { value } = e.target

          clientIsGDPR = gdprCountriesIds.indexOf(Number(value)) > -1
          userCountryId = value

          updateSubmitState(_els, isNS)
          if (questions.length) {
            if (clientIsGDPR) {
              hideQuestions({ questions }) // wait for consent
            } else {
              showQuestions({ questions })
            }
          }
          updateHiddens({ ccbox, fieldset, hiddenC })
          updateQsRequiredState(questions, clientIsGDPR)
          if (!clientIsGDPR) ccbox.checked = false
        })

        if (isNS && allCountries) {
          allCountries.map(country => {
            const opt = document.createElement('option')
            opt.value = country.id
            opt.textContent = country.name
            select.append(opt)
          })
        }

        if (value) {
          select.dispatchEvent(new Event('change'))
        }
      }
    }
  }
}
