export default {
  init (cfg, ctx) {
    const { notificationsInstances, read, readAll } = this
    const { event } = ctx

    if (notificationsInstances.length < 1) {
      return false
    }

    event.on('notificationsReadAll', () => { readAll() })

    event.on('notificationRead', e => {
      const { id } = e
      for (const instance of notificationsInstances) {
        read(instance.querySelector(`[data-id="${id}"]`))
      }
    })

    document.addEventListener('click', e => {
      const { target } = e
      const { nodeName, classList, parentNode } = target

      if (nodeName !== 'BUTTON' ||
        !(classList.contains('read') &&
          (parentNode.classList.contains('unread') ||
            classList.contains('all')))) {
        return
      }

      const all = classList.contains('all')
      if (all) {
        event.trigger('notificationsReadAll')
        return
      }
      const { id } = parentNode.dataset

      if (!id) {
        throw new Error('Notification missing id! Could not mark as read.')
      }
      event.trigger('notificationRead', { id })
    })
  },

  get notificationsInstances () {
    return document.querySelectorAll('.notifications')
  },

  read (notification) {
    if (!notification) {
      return
    }
    notification.classList.remove('unread')
    const button = notification.querySelector('button.read')
    if (button) {
      button.remove()
    }
  },

  readAll () {
    const all = document.querySelectorAll('.notifications li.unread[data-id]')
    let ids = []

    for (const notif of all) {
      const { id } = notif.dataset
      if (ids.indexOf(String(id)) < 1) {
        ids.push(id)
      }
      notif.classList.remove('unread')
    }
  }
}
