// import { lory } from 'lory.js'
// import Debug from 'debug'
// const debug = Debug('InfoQ:guides-slider')

export default {
  get guidesSlider () {
    return document.querySelector('.homepage [data-tax="guides"]')
  },
  set activeIndex (index) {
    const paginator = this.guidesSlider.querySelector('.carousel__paginator')
    const { children, parentNode } = paginator
    let i = 1
    for (const child of children) {
      // debug(i, index)
      child.classList.toggle('active', i === index)
      i++
    }
    parentNode.dataset.activeIndex = index
  },

  init (config) {
    this.config = config
    const { guidesSlider } = this
    if (!guidesSlider) {
      // debug('No slider on page.')
      return
    }
    const { children, classList } = guidesSlider
    const { length } = children

    if (length < 5) {
      // debug('Not enough items to pop the slider')
      return
    }

    classList.add('carousel')

    const nav = document.createElement('NAV')
    nav.classList.add('carousel__paginator')

    const activeIndex = 1
    this.slideIndexes = Math.ceil(length / 4)

    for (let i = 1; i <= this.slideIndexes; i++) {
      const dot = document.createElement('A')
      const { classList, dataset } = dot
      dataset.index = i
      if (i === activeIndex) {
        classList.add('active')
      }
      nav.append(dot)
    }
    guidesSlider.append(nav)

    document.addEventListener('click', e => {
      const { target } = e
      const { nodeName, parentNode, classList, dataset } = target
      if (nodeName !== 'A' ||
        !parentNode.classList.contains('carousel__paginator') ||
        classList.contains('active')) {
        return
      }
      this.activeIndex = Number(dataset.index)

      // debug('switched slide', dataset.index)
    })
  }
}
